<template>
  <div>
    <!-- 메인 콘텐츠 영역 -->
    <div class="content">
      <h2 class="content-title">사용자 관리</h2>
      <p class="content-sub-title">사용자의 정보를 관리하는 페이지</p>

      <div class="search-controls">
        <!-- 부서, 팀 선택 그룹 -->
        <div class="d-flex align-items-center department-group mt-2">
          <DefaultLabel text="부서 :" forId="department" size="small" />
          <DefaultSelect
            id="department"
            v-model="selectedDepartment"
            :options="departmentsOptions"
            placeholder="부서 선택"
            @change="updateTeams"
            size="small"
            marginRight="10px"
          />
          <DefaultLabel text="팀 :" forId="team" size="small" />
          <DefaultSelect
            id="team"
            v-model="selectedTeam"
            :options="teamsOptions"
            placeholder="팀 선택"
            size="small"
          />
        </div>
        <!-- 이름 검색 그룹 -->
        <div class="d-flex align-items-center name-group mt-2">
          <DefaultLabel text="이름 :" forId="nameSearch" size="small" />
          <DefaultTextfield
            type="text"
            id="nameSearch"
            v-model="nameSearch"
            placeholder="이름(이메일)"
          />
          <DefaultButton
            @click="search"
            size="small">
            조회
          </DefaultButton>
        </div>
      </div>

      <hr/>

      <!-- 테이블 보기 (스크롤로 10개 정도 보이도록) -->
      <DefaultTable
        :noColumns="true"
        :columns="columns"
        :data="filteredData"
        :rowClick="(item) => showModal(item)"
        :ellipsisWidth="50"
      />

      <!-- 사용자 세부 정보 모달을 분리한 컴포넌트 -->
      <UserManagementModifyModal
        :visible="modalVisible"
        :isAdmin="isAdmin"
        :user="selectedUser"
        @close="modalVisible = false"
        @confirmSave="handleSaveClick"
      />

      <!-- 저장 확인 모달 -->
      <AlertModal 
        :isVisible="confirmationModalVisible" 
        :disableBackgroundClose="true"
        title="저장 확인" 
        cancelText="취소"
        confirmText="확인" 
        @close="confirmationModalVisible = false" 
        @confirm="saveUserDetails"
      >
        <template #body>
          <p>변경 사항을 저장하시겠습니까?</p>
          <p v-if="statusChanged" style="color: red;">입사/퇴사 처리가 포함되어 있습니다.</p>
        </template>
      </AlertModal>

      <!-- 변경된 데이터가 없을 때 표시할 모달 -->
      <AlertModal 
        :isVisible="noChangesModalVisible" 
        title="알림" 
        confirmText="확인" 
        @close="closeNoChangesModal"
        @confirm="closeNoChangesModal"
      >
        <template #body>
          <p>변경된 데이터가 없습니다.</p>
        </template>
      </AlertModal>

      <!-- 저장 완료 모달 -->
      <AlertModal 
        :isVisible="saveCompleteModalVisible" 
        :title="saveCompleteMessageTitle" 
        confirmText="확인"
        @close="closeSaveCompleteModal" 
        @confirm="closeSaveCompleteModal"
      >
        <template #body>
          <p>{{ saveCompleteMessage }}</p>
        </template>
      </AlertModal>
    </div>
  </div>
</template>

<script setup>
import { ref, computed, onMounted } from 'vue';
import axios from 'axios';
import AlertModal from '@/components/common/modal/AlertModal.vue';
import DefaultButton from '@/components/common/button/DefaultButton.vue';
import UserManagementModifyModal from '@/components/hrm/UserManagementModifyModal.vue';
import DefaultTable from '@/components/common/table/DefaultTable.vue';
import DefaultSelect from '@/components/common/select/DefaultSelect.vue';
import DefaultTextfield from '@/components/common/textfield/DefaultTextfield.vue';
import DefaultLabel from '@/components/common/label/DefaultLabel.vue';

// 관리자 여부
const isAdmin = ref(true);

// 모달 관련 상태
const modalVisible = ref(false);
const confirmationModalVisible = ref(false);
const saveCompleteModalVisible = ref(false);
const noChangesModalVisible = ref(false);
const saveCompleteMessageTitle = ref('저장 완료');
const saveCompleteMessage = ref('저장되었습니다.');

// 테이블 컬럼
const columns = [
  { key: 'name',       label: '이름', width: 60 },
  { key: 'email',      label: '이메일', width: 160 }, 
  { key: 'department', label: '부서', width: 100 }, 
  { key: 'team',       label: '팀', width: 100 },
  { key: 'position',   label: '직책', width: 60 },
  { key: 'status',     label: '상태', width: 50 },
];

// 사용자 선택 및 원본 정보
const selectedUser = ref({
  name: '',
  email: '',
  password: '',
  department: '',
  team: '',
  position: '',
  joinDate: '',
  leaveDate: '',
  status: '입사',
  birthDate: '',
  employmentType: '정규직',
  zipCode: '',
  address: '',
  location: ''
});
const originalUser = ref({ ...selectedUser.value });

// 상태 변경 감지 (입사/퇴사 등)
const statusChanged = ref(false);
// 데이터 변경 감지
const dataChanged = ref(false);

// 부서 및 팀 선택을 위한 상태
const selectedDepartment = ref('');
const selectedTeam = ref('');
const departments = ref([]);
const teams = ref({});
const availableTeams = ref([]);

// 부서, 팀 데이터 가져오기
const fetchDepartments = async () => {
  try {
    // 예시 데이터를 직접 할당
    const departmentData = [
      {
        "name": "미지정",
        "teams": ["영업"]
      },
      {
        "name": "SI사업본부",
        "teams": ["프레임워크", "미지정"]
      },
      {
        "name": "AI사업본부",
        "teams": ["RPA"]
      },
      {
        "name": "경영관리부",
        "teams": ["경영", "영업"]
      }
    ];

    departments.value = departmentData.map(dep => dep.name);
    teams.value = departmentData.reduce((acc, dep) => {
      acc[dep.name] = dep.teams;
      return acc;
    }, {});
  } catch (error) {
    console.error('Error fetching departments:', error);
  }
};

// 부서 변경 시 팀 업데이트
const updateTeams = () => {
  availableTeams.value = teams.value[selectedDepartment.value] || [];
  selectedTeam.value = '';
};

// 부서 옵션 (DefaultSelect - computed)
// 각 부서를 { value, label } 객체로 변환하며 첫 옵션은 안내 문구
const departmentsOptions = computed(() => {
  return [{ value: '', label: '부서 선택' }, ...departments.value.map(dep => ({ value: dep, label: dep }))];
});

// 팀 옵션 (DefaultSelect - computed)
const teamsOptions = computed(() => {
  return [{ value: '', label: '팀 선택' }, ...availableTeams.value.map(team => ({ value: team, label: team }))];
});

// 사용자 리스트(예시)
const data = ref([
  { name: '권범준', email: 'qjawns0618@kpcnc.co.kr', password: '7777', department: 'SI사업본부', team: '프레임워크', position: '책임', joinDate: '2022-01-01', leaveDate: '', status: '입사', birthDate: '1990-01-01', employmentType: '정규직', zipCode: '12345', address: '서울 강남구', location: '서울' },
  { name: '권범준', email: 'qjawns0618@kpcnc.co.kr', password: '7777', department: 'SI사업본부', team: '프레임워크', position: '책임', joinDate: '2022-01-01', leaveDate: '', status: '입사', birthDate: '1990-01-01', employmentType: '정규직', zipCode: '12345', address: '서울 강남구', location: '서울' },
  { name: '권범준', email: 'qjawns0618@kpcnc.co.kr', password: '7777', department: 'SI사업본부', team: '프레임워크', position: '책임', joinDate: '2022-01-01', leaveDate: '', status: '입사', birthDate: '1990-01-01', employmentType: '정규직', zipCode: '12345', address: '서울 강남구', location: '서울' },
  { name: '권범준', email: 'qjawns0618@kpcnc.co.kr', password: '7777', department: 'SI사업본부', team: '프레임워크', position: '책임', joinDate: '2022-01-01', leaveDate: '', status: '입사', birthDate: '1990-01-01', employmentType: '정규직', zipCode: '12345', address: '서울 강남구', location: '서울' },
  { name: '김철수', email: 'kim@example.com', password: '1237', department: 'AI사업본부', team: 'RPA', position: '책임', joinDate: '2021-07-15', leaveDate: '2023-07-11', status: '퇴사', BirthDate: '1985-06-15', employmentType: '비정규직', zipCode: '67890', address: '부산 해운대구', location: '부산' },
  { name: '김철수', email: 'kim@example.com', password: '1237', department: 'AI사업본부', team: 'RPA', position: '책임', joinDate: '2021-07-15', leaveDate: '2023-07-11', status: '퇴사', BirthDate: '1985-06-15', employmentType: '비정규직', zipCode: '67890', address: '부산 해운대구', location: '부산' },
  { name: '이영희', email: 'lee@example.com', password: '1234', department: '경영관리부', team: '영업', position: '선임', joinDate: '2023-03-12', leaveDate: '', status: '입사', BirthDate: '1995-03-12', employmentType: '정규직', zipCode: '54321', address: '인천 서구', location: '인천' },
  { name: '이영희', email: 'lee@example.com', password: '1234', department: '경영관리부', team: '영업', position: '선임', joinDate: '2023-03-12', leaveDate: '', status: '입사', BirthDate: '1995-03-12', employmentType: '정규직', zipCode: '54321', address: '인천 서구', location: '인천' },
  { name: '이영희', email: 'lee@example.com', password: '1234', department: '경영관리부', team: '영업', position: '선임', joinDate: '2023-03-12', leaveDate: '', status: '입사', BirthDate: '1995-03-12', employmentType: '정규직', zipCode: '54321', address: '인천 서구', location: '인천' },
  { name: '김지수', email: 'su@example.com', password: '1234', department: 'SI사업본부', team: '미지정', position: '선임', joinDate: '2024-07-12', leaveDate: '', status: '입사', BirthDate: '1992-03-12', employmentType: '정규직', zipCode: '7777', address: '강원', location: '속초' },
  { name: '김지수', email: 'su@example.com', password: '1234', department: 'SI사업본부', team: '미지정', position: '선임', joinDate: '2024-07-12', leaveDate: '', status: '입사', BirthDate: '1992-03-12', employmentType: '정규직', zipCode: '7777', address: '강원', location: '속초' },
  { name: '김지수', email: 'su@example.com', password: '1234', department: 'SI사업본부', team: '미지정', position: '선임', joinDate: '2024-07-12', leaveDate: '', status: '입사', BirthDate: '1992-03-12', employmentType: '정규직', zipCode: '7777', address: '강원', location: '속초' },
  { name: '김지수', email: 'su@example.com', password: '1234', department: 'SI사업본부', team: '미지정', position: '선임', joinDate: '2024-07-12', leaveDate: '', status: '입사', BirthDate: '1992-03-12', employmentType: '정규직', zipCode: '7777', address: '강원', location: '속초' },
]);

// 검색어
const nameSearch = ref('');

// 필터링된 데이터
const filteredData = computed(() => {
  return data.value.filter(item => {
    const departmentMatch = selectedDepartment.value
      ? item.department === selectedDepartment.value
      : true;
    const teamMatch = selectedTeam.value
      ? item.team === selectedTeam.value
      : true;
    const nameMatch = nameSearch.value
      ? item.name.includes(nameSearch.value)
      : true;
    return departmentMatch && teamMatch && nameMatch;
  });
});

// 검색
const search = () => {
  // 기존엔 페이지네이션 currentPage=1로 이동했지만 이제 페이징 없음
  // 필요 없으면 비워둠
};

// 사용자 클릭 시 모달 열기
const showModal = (user) => {
  selectedUser.value = { ...user };
  originalUser.value = { ...user };
  statusChanged.value = false;
  dataChanged.value = false;
  modalVisible.value = true;
};

// 모달에서 저장버튼 누를 때(자식 컴포넌트 @confirmSave 이벤트)
const handleSaveClick = ({ isMismatch, isChanged, userData, statusChangedFlag }) => {
  // 비밀번호 불일치인 경우
  if (isMismatch) {
    saveCompleteMessageTitle.value = '비밀번호 불일치';
    saveCompleteMessage.value = '비밀번호와 재확인의 값이 일치하지 않습니다.';
    saveCompleteModalVisible.value = true;
    return;
  }

  // 변경 사항 없는 경우
  if (!isChanged) {
    noChangesModalVisible.value = true;
    return;
  }

  // userData로 부모 컴포넌트의 selectedUser 갱신
  selectedUser.value = { ...userData };

  // 상태 변경 여부 저장해둠 (입사/퇴사)
  statusChanged.value = statusChangedFlag;

  // 최종 저장 확인 모달 열기
  confirmationModalVisible.value = true;
};

// 실제로 DB 저장 호출
const saveUserDetails = () => {
  // 모달 닫기
  modalVisible.value = false;
  confirmationModalVisible.value = false;

  axios.post('/api/saveUser', selectedUser.value)
    .then(response => {
      console.log(response.data);
      // 성공 시
      saveCompleteMessageTitle.value = '저장 완료';
      saveCompleteMessage.value = '저장되었습니다.';
      saveCompleteModalVisible.value = true;
    })
    .catch(error => {
      console.log(error);
      // 실패 시
      saveCompleteMessageTitle.value = '저장 실패';
      saveCompleteMessage.value = '저장에 실패했습니다.(기능 미구현)';
      saveCompleteModalVisible.value = true;
    });
};

// '변경된 데이터가 없습니다' 모달 닫기
const closeNoChangesModal = () => {
  noChangesModalVisible.value = false;
};

// 저장 완료 모달 닫기
const closeSaveCompleteModal = () => {
  saveCompleteModalVisible.value = false;
};

// onMounted 시 부서 정보 로딩
onMounted(() => {
  fetchDepartments();
});
</script>

<style scoped>
hr {
  margin-bottom: 0px;
}

/* 검색 영역 반응형 */
.label-team {
  margin-left: 10px;
}

/* 테이블 스타일 */
.table {
  width: 100%;
  margin: 0; /* 상단 마진은 .table-container 쪽에서 처리 */
}
  
/* 테이블 컨테이너에 스크롤 추가하여 10개 행만 보이는 정도의 높이 설정(대략 350~400px) */
.table-container {
  max-height: 400px; /* 원하는 높이로 조절 */
  overflow-y: auto;
  margin-bottom: 60px;
}

@media (min-width: 1201px) {
  .search-controls {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-end;
  }
  .search-controls > div {
    margin-right: 1rem;
    margin-bottom: 0;
  }
  #nameSearch {
    font-size: 1rem;
  }
}

@media (max-width: 1200px) {
  .search-controls {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }
  .search-controls > div {
    width: 100%;
    display: flex;
    justify-content: flex-end;
  }
}
</style>
