<template>
  <div :class="rowClass">
    <slot></slot>
  </div>
</template>

<script setup>
import { computed, defineProps } from 'vue';

const props = defineProps({
  // 정렬 옵션: 'left'(기본), 'center', 'right'
  align: {
    type: String,
    default: 'left',
    validator: value => ['left', 'center', 'right'].includes(value)
  },
  // 추가적으로 적용할 클래스
  customClass: {
    type: String,
    default: ''
  }
});

// align prop에 따라 정렬 클래스를 결정합니다.
const rowClass = computed(() => {
  let alignmentClass = '';
  switch (props.align) {
    case 'center':
      alignmentClass = 'justify-content-center';
      break;
    case 'right':
      alignmentClass = 'justify-content-end';
      break;
    case 'left':
    default:
      alignmentClass = 'justify-content-start';
  }
  // 기본 클래스: d-flex, align-items-center, mt-2 등을 포함합니다.
  return `default-form-row d-flex align-items-center mt-2 ${alignmentClass} ${props.customClass}`.trim();
});
</script>

<style scoped>
.default-form-row {
  width: 100%;
}
</style>
