<template>
  <!-- 부트스트랩 모달 -->
  <!-- onMounted에서 부트스트랩 설정 부분에서도 가능함 -->
  <!-- data-bs-backdrop="static" : 백드롭 클릭시 닫기 방지 -->
  <!-- data-bs-keyboard="false" : ESC 키 닫기 방지 -->
  <div 
    class="modal fade" 
    id="noticeModal" 
    tabindex="-1" 
    aria-labelledby="noticeModalLabel" 
    aria-hidden="true"
    data-bs-backdrop="static"
    data-bs-keyboard="false"
  >
    <div class="modal-dialog modal-dialog-centered">
      <div class="modal-content">
        <!-- 모달 헤더 -->
        <div class="modal-header">
          <h5 class="modal-title" id="noticeModalLabel">공지 사항</h5>
          <button 
            type="button" 
            class="btn-close" 
            data-bs-dismiss="modal" 
            aria-label="Close"
            @click="closeModal"
          ></button>
        </div>

        <!-- 모달 본문 -->
        <div class="modal-body">
          <form>
            <div class="mb-3">
              <label for="noticeTitle" class="form-label">제목</label>
              <input 
                type="text" 
                class="form-control" 
                id="noticeTitle" 
                v-model="localForm.title" 
                :disabled="!props.isAdmin" 
                placeholder="제목을 입력하세요"
              >
            </div>
            <div class="mb-3">
              <label for="noticeAuthor" class="form-label">작성자</label>
              <input 
                type="text" 
                class="form-control" 
                id="noticeAuthor" 
                v-model="localForm.author" 
                :disabled="!props.isAdmin"
                placeholder="작성자를 입력하세요"
              >
            </div>
            <div class="mb-3">
              <label for="noticeDate" class="form-label">작성일</label>
              <input 
                type="date" 
                class="form-control" 
                id="noticeDate" 
                v-model="localForm.date" 
                disabled
              >
            </div>
            <div class="mb-3">
              <label for="noticeContent" class="form-label">내용</label>
              <textarea 
                class="form-control" 
                id="noticeContent" 
                v-model="localForm.content" 
                :disabled="!props.isAdmin" 
                rows="4" 
                placeholder="내용을 입력하세요"
              ></textarea>
            </div>
            <div v-if="!props.isAdmin" class="mb-3 form-check">
              <input 
                type="checkbox" 
                class="form-check-input" 
                id="cookieConsent" 
                v-model="localForm.cookieConsent"
              >
              <label class="form-check-label" for="cookieConsent">
                해당 공지를 다시 띄우지 않음(쿠키 사용 동의)
              </label>
            </div>
          </form>
        </div>

        <!-- 모달 푸터 -->
        <div class="modal-footer">
          <button 
            type="button" 
            class="btn btn-secondary" 
            data-bs-dismiss="modal" 
            @click="closeModal"
          >
            닫기
          </button>
          <!-- 등록 버튼 (isCreate === true) -->
          <button 
            type="button" 
            class="btn btn-primary" 
            v-if="props.isAdmin && props.isCreate" 
            @click="saveNotice"
          >
            등록
          </button>
          <!-- 수정 버튼 (isCreate === false) -->
          <button 
            type="button" 
            class="btn btn-primary" 
            v-if="props.isAdmin && !props.isCreate" 
            @click="saveNotice"
          >
            수정
          </button>
          <!-- 삭제 버튼 (isCreate === false) -->
          <button 
            type="button" 
            class="btn btn-danger" 
            v-if="props.isAdmin && !props.isCreate"
            @click="showDeleteAlert"
          >
            삭제
          </button>
        </div>
      </div>
    </div>
  </div>

  <!-- 삭제 확인 AlertModal (자식 안에 배치) -->
  <AlertModal 
    :isVisible="deleteConfirmVisible" 
    :disableBackgroundClose="false"
    title="삭제 확인" 
    confirmText="확인" 
    cancelText="취소"
    @close="deleteConfirmVisible = false" 
    @confirm="deleteNotice"
  >
    <template #body>
      <p>삭제하시겠습니까?</p>
    </template>
  </AlertModal>
</template>

<script setup>
import { ref, watch, defineProps, defineEmits, onMounted } from 'vue';
import * as bootstrap from 'bootstrap';
import AlertModal from '@/components/common/modal/AlertModal.vue';

/**
 * 부모로부터 넘어오는 props
 *  - isAdmin: 관리자 여부
 *  - isCreate: 등록/수정 구분
 *  - form: 원본 폼 데이터
 */
const props = defineProps({
  isAdmin: Boolean,
  isCreate: Boolean,
  form: Object,
});

/**
 * 이벤트:
 *  - close: 모달 닫기
 *  - save: 등록/수정 완료 시 부모로 전달
 *  - delete: 삭제 확인 시 부모로 전달
 */
const emit = defineEmits(['close', 'save', 'delete']);

/** localForm: 부모 form을 복사해서 사용 */
const localForm = ref({ ...props.form });
watch(
  () => props.form,
  (newForm) => {
    localForm.value = { ...newForm };
  },
  { deep: true }
);

/** 모달 인스턴스 & 삭제 확인 모달 표시 여부 */
const modalInstance = ref(null);
const deleteConfirmVisible = ref(false);

/** onMounted에서 Bootstrap 모달 초기화 & 표시 */
onMounted(() => {
  const modalElement = document.getElementById('noticeModal');
  modalInstance.value = new bootstrap.Modal(modalElement, {
    // backdrop: 'static', // 백드롭 클릭시 닫기 방지
    // keyboard: false     // ESC 키 닫기 방지
  });
  // 모달 열기
  modalInstance.value.show();
});

/** 닫기 버튼 */
function closeModal() {
  modalInstance.value.hide();
  emit('close');
}

/** 등록/수정 버튼 */
function saveNotice() {
  // localForm 데이터 부모에 전달
  emit('save', { ...localForm.value });
  modalInstance.value.hide();
  emit('close');
}

/** 삭제 버튼 클릭 => AlertModal 열기 */
function showDeleteAlert() {
  deleteConfirmVisible.value = true;
}

/** AlertModal에서 확인 시 => 부모에 삭제 이벤트 알림 */
function deleteNotice() {
  // 현재 항목 ID를 부모에 전달
  emit('delete', localForm.value.id);
  deleteConfirmVisible.value = false;
  modalInstance.value.hide();
  emit('close');
}
</script>

<style scoped>
.modal-header {
  display: flex;
  justify-content: space-between;
  font-size: 1.5rem;
}
</style>
