<template>
  <div class="centered-container">
    <div>
      <v-card
        class="mx-auto pa-12 pb-8"
        elevation="8"
        max-width="448"
        rounded="lg"
      >
        <v-img
          class="mx-auto my-6 header-logo"
          max-width="228"
          src="https://www.kpcnc.co.kr/src/img/logo.png"
        ></v-img><br>

        <div class="text-subtitle-1 text-medium-emphasis">Account</div>

        <v-text-field
          v-model="username"
          density="compact"
          placeholder="Email address"
          prepend-inner-icon="mdi-email-outline"
          variant="outlined"
        ></v-text-field>

        <div class="text-subtitle-1 text-medium-emphasis d-flex align-center justify-space-between">
          Password
          <a
            class="text-caption text-decoration-none text-blue"
            href="#"
            rel="noopener noreferrer"
            target="_blank"
          >
            Forgot login password?
          </a>
        </div>

        <v-text-field
          v-model="password"
          :append-inner-icon="visible ? 'mdi-eye-off' : 'mdi-eye'"
          :type="visible ? 'text' : 'password'"
          density="compact"
          placeholder="Enter your password"
          prepend-inner-icon="mdi-lock-outline"
          variant="outlined"
          @click:append-inner="visible = !visible"
        ></v-text-field>

        <v-card
          class="mb-12 warning-text"
          color="surface-variant"
          variant="tonal"
        >
          <v-card-text class="text-medium-emphasis text-caption">
            Warning: After 3 consecutive failed login attempts, your account will be temporarily locked for three hours. Unauthorized users accessing this system may be subject to penalties. Please use your account responsibly.
          </v-card-text>
        </v-card>
        <v-btn
          class="mb-8 btn-login"
          color="blue"
          size="large"
          variant="tonal"
          block
          @click="login"
        >
          Sign In
        </v-btn>
        <v-btn
          class="mb-8 btn-join"
          color="blue"
          size="large"
          variant="tonal"
          block
          @click="join"
        >
          Sign Up
        </v-btn>
        <v-btn
          class="mb-8 btn-google"
          color="red"
          size="large"
          variant="tonal"
          block
          @click="oauth('google')"
        >
          Google
        </v-btn>
        <v-btn
          class="mb-8 btn-google"
          color="yellow"
          size="large"
          variant="tonal"
          block
          @click="oauth('kakao')"
        >
          Kakao
        </v-btn>
        <img class="footer-logo" src="https://www.kpcnc.co.kr/src/img/logo.png" alt="logo">
      </v-card>
      <div class="footer-text">
        CENTRALLY SYSTEM. Copyright 2024 KPCNC. All rights reserved.
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, onBeforeMount } from 'vue';
import { useRouter, useRoute } from 'vue-router';
import LoginApi from '@/api/auth/LoginApi';
import { useAuthStore } from '@/store/auth';

// 상태 관리
const visible = ref(false);

// 라우터 사용 설정
const router = useRouter()
const route = useRoute();

const username = ref('');
const password = ref('');
const authStore = useAuthStore();

// 소셜 로그인/일반 로그인 후, 사용자 정보를 가져오는 함수
const fetchUserInfo = async () => {
  try {

    console.log('fetchUserInfo');

    const response = await LoginApi.getMe(); // GET /me
    // 예: response.data = { username: 'aaa', roles: ['ROLE_USER'] }

    console.log('사용자 정보:', response.data);

    // 피니아에 저장
    authStore.login({
      username: response.data.username,
      roles: response.data.roles,
    });

    // 로그인 성공 후 원하는 페이지 이동
    router.push('/');

  } catch (error) {
    console.error('사용자 정보를 가져오는 중 오류가 발생했습니다:', error);
    alert('로그인에 실패했거나, 사용자 정보를 불러올 수 없습니다.');
  }
};

// 로그인 함수
const login = async () => {
  try {
    const response = await LoginApi.login(username.value, password.value);
    if (response.status === 200) {
      await fetchUserInfo();
    } else {
      alert('로그인 실패! 아이디와 비밀번호를 확인하세요.');
    }
  } catch (error) {
    alert('로그인 오류가 발생했습니다.');
  }
};

// 소셜 종류(google, kakao)에 따라 다른 엔드포인트로 이동
const oauth = (social) => {
  if (social === 'google') {
    // (예) 구글 OAuth2 로그인 엔드포인트로 이동
    // 스프링 백엔드(Security)에서 설정한 /oauth2/authorization/google
    window.location.href = 'http://localhost:8080/auth/oauth2/authorization/google';
  } else if (social === 'kakao') {
    window.location.href = 'http://localhost:8080/auth/oauth2/authorization/kakao';
  } else {
    alert('알 수 없는 소셜 로그인 방식입니다.');
  }
};

// 회원가입
const join = () => {
  console.log('join clicked');
  router.push('/join');
};

// 페이지 뜨기 전, 로그인 여부 확인 → 이미 로그인됐다면 / 로 이동
onBeforeMount(() => {
  const search = route.query.search;
  if (search) {
    alert('일반 가입 후 소셜 연동을 할 수 있습니다.');
    return // 아래 로직은 더 이상 진행 X
  }
  if (authStore.isAuthenticated) {
    router.push('/');
  }
});
</script>

