<template>
  <nav class="navbar navbar-light">
    <router-link to="/">
      <a class="navbar-brand" href="/hrm/user-information">
        <img src="https://www.kpcnc.co.kr/src/img/logo.png" class="d-inline-block align-top" alt="Logo">
      </a>
    </router-link>
    <div class="navbar-text">
      <router-link to="/hrm/user-information" class="user-name">
        <img
          src="https://avatars.githubusercontent.com/u/48048508?v=4"
          class="rounded-circle user-image"
          alt="User Image"
        >
        권 범 준
      </router-link>
      <div>&nbsp;|&nbsp;</div>
      <!-- 로그아웃 버튼 - 클릭 시 doLogout() 호출 -->
      <a href="#" class="user-name" @click.prevent="doLogout">
        로그아웃
      </a>
      <i class="fas fa-caret-down user-icon"></i> <!-- 아이콘 추가 -->
    </div>
  </nav>
</template>

<script setup>
import LoginApi from '@/api/auth/LoginApi';
import { useRouter } from 'vue-router'
import { useAuthStore } from '@/store/auth'

// Vue Router, Pinia 스토어
const router = useRouter()
const authStore = useAuthStore()

/**
 * 로그아웃 함수:
 * 1) 백엔드 /auth/logout 요청
 * 2) authStore.logout() → Pinia 스토어 상태 초기화
 * 3) 로그인 페이지로 라우팅
 */
const doLogout = async () => {
  try {
    // 백엔드에 로그아웃 요청
    const response = await LoginApi.logout();
    if (response.status === 200) {
      alert('로그아웃 성공!!');
    } else {
      alert('로그아웃 실패!!');
    }

    // Pinia 스토어에서 사용자 상태 초기화
    authStore.logout()

    // 로그인 페이지 등 원하는 경로로 이동
    router.push('/login')
  } catch (error) {
    console.error('로그아웃 요청 중 오류:', error)
    alert('로그아웃 처리에 실패했습니다.')
  }
}
</script>

