<template>
  <div>
    <!-- 메인 콘텐츠 영역 -->
    <div class="content">
      <h2 class="content-title">영수증 결제 마감 확인</h2>
      <p class="content-sub-title">결제 마감 후 정상적 처리 확인 페이지</p>

      <div class="search-controls">
        <!-- 월 선택 그룹 (오른쪽 정렬) -->
        <div class="d-flex align-items-center end-date-group mt-2 justify-content-end">
          <DefaultLabel text="월 선택 :" forId="months" size="medium" />
          <DefaultTextfield
            type="month"
            id="months"
            v-model="endDate"
            size="small"
          />
        </div>
        <!-- 이름 검색 그룹 (실시간 필터링 → 여기서는 백엔드로) -->
        <div class="d-flex align-items-center name-group mt-2 justify-content-end">
          <DefaultLabel text="이름 :" forId="nameSearch" size="medium" />
          <DefaultTextfield
            type="text"
            id="nameSearch"
            v-model="nameSearch"
            size="small"
            placeholder="이름 입력"
          />
          <DefaultButton @click="search" size="small">
            조회
          </DefaultButton>
        </div>
      </div>

      <!-- (큰 화면) DefaultTable 컴포넌트 -->
      <DefaultTable
        :showTable="!isMobile"
        :columns="columns"
        :data="data"
      />

      <!-- (작은 화면) 카드 형식 보기 -->
      <div class="card-layout" v-if="isMobile">
        <div class="card" v-for="(item, index) in data" :key="index">
          <div class="card-header">
            <p class="card-title">{{ item.name }}</p>
          </div>
          <div class="card-body">
            <p class="card-text"><Strong>건수: </Strong>{{ item.count }}</p>
            <p class="card-text"><Strong>부서: </Strong>{{ item.department }}</p>
            <p class="card-text"><Strong>팀: </Strong>{{ item.team }}</p>
            <p class="card-text"><Strong>승인금액: </Strong>{{ formatCurrency(item.approved) }}원</p>
            <p class="card-text"><Strong>반려금액: </Strong>{{ formatCurrency(item.rejected) }}원</p>
          </div>
        </div>
      </div>

      <!-- 페이지네이션 (공통 컴포넌트) -->
      <DefaultPagination
        :currentPage="currentPage"
        :totalPages="totalPages"
        :visiblePageCount="visiblePageCount"
        @pageChange="onPageChange"
      />
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted } from 'vue';
import DefaultTable from '@/components/common/table/DefaultTable.vue';
import DefaultPagination from '@/components/common/pagination/DefaultPagination.vue';
import DefaultButton from '@/components/common/button/DefaultButton.vue';
import DefaultTextfield from '@/components/common/textfield/DefaultTextfield.vue';
import DefaultLabel from '@/components/common/label/DefaultLabel.vue';
// import axios from 'axios'; // 실제 백엔드 호출 시

/**
 * 상태 정의
 */
const endDate = ref('');
const nameSearch = ref('');
const currentPage = ref(1);
const totalPages = ref(1);    // 서버 응답값
const data = ref([]);         // 서버에서 받아온 데이터
const isMobile = ref(false);
const visiblePageCount = ref(5);  // 페이지네이션에서 보여줄 최대 페이지 버튼 수

/** 테이블의 컬럼 정의 */
const columns = [
  { key: 'name', label: '이름', width: 60 },
  { key: 'department', label: '부서', width: 100 },
  { key: 'team', label: '팀', width: 100 },
  { key: 'count', label: '건수', width: 50 },
  { key: 'approved', label: '승인금액', width: 100 },
  { key: 'rejected', label: '반려금액', width: 100 },
];

/**
 * 백엔드 호출 (page, endDate, nameSearch 등 파라미터 전달)
 * 실제로는 axios 활용
 */
// async function fetchDataFromServer(page = 1) {
async function fetchDataFromServer() {
  try {
    // const response = await axios.get('/api/marginCheck', {
    //   params: {
    //     page,
    //     endDate: endDate.value,
    //     name: nameSearch.value
    //   }
    // });
    // data.value = response.data.items;
    // totalPages.value = response.data.totalPages;

    // 예시 mock
    const mockData = [
      {
        name: '김필수',
        department: 'SI사업본부',
        team: '프레임워크',
        count: 3,
        approved: 800000,
        rejected: 200000
      },
      {
        name: '홍길동',
        department: 'SI사업본부',
        team: '프레임워크',
        count: 3,
        approved: 800000,
        rejected: 200000
      },
      {
        name: '김한',
        department: 'SI사업본부',
        team: '프레임워크',
        count: 5,
        approved: 1000000,
        rejected: 200000
      },
      // ...추가 데이터
    ];
    data.value = mockData;
    totalPages.value = 3; // 예시
  } catch (err) {
    console.error(err);
  }
}

/**
 * 검색 함수
 * - endDate, nameSearch가 바뀔 때마다 page=1 로 백엔드 다시 호출
 */
function search() {
  currentPage.value = 1;
  fetchDataFromServer(currentPage.value);
}

/**
 * 페이지 변경 시 (DefaultPagination에서 방출)
 * - newPage에 맞춰 백엔드 호출
 */
function onPageChange(newPage) {
  currentPage.value = newPage;
  fetchDataFromServer(newPage);
}

/**
 * 반응형 여부 판단
 */
function updateViewMode() {
  isMobile.value = window.innerWidth <= 1200;
}

/**
 * 금액 표시 포맷
 */
function formatCurrency(amount) {
  if (!amount) return '0';
  return amount.toLocaleString();
}

/**
 * onMounted
 */
onMounted(() => {
  // 페이지 로드시 1페이지 호출
  fetchDataFromServer(currentPage.value);

  // 뷰포트 너비 체크
  updateViewMode();
  window.addEventListener('resize', updateViewMode);
});
</script>


<style scoped>
#dataTable tbody tr:hover td {
  background-color: #e7f1ff !important;
}
.search-controls {
  margin-bottom: 15px;
}
@media (min-width: 1201px) {
  .card {
    display: none; /* 카드 숨기기 */
  }
  .table {
    display: table; /* 테이블 표시 */
  }
}
</style>
