    <!-- 메인 콘텐츠 영역 -->
    <div class="content">
      <h2 class="content-title">영수증 신청내역 현황</h2>
      <!-- 검색된 날짜 범위 표시 -->
      <div class="content-sub-title" v-if="data.length">
        <p>날짜: [{{ startDate }} ~ {{ endDate }}]</p>
      </div>

      <div class="search-controls">
        <!-- 시작, 종료일 그룹 -->
