          />
          <DefaultButton 
            @click="search"
            size="small">
            조회
          </DefaultButton>
        </div>
      </div>

      <!-- DefaultTable 컴포넌트 (큰 화면) -->
      <DefaultTable
        :columns="columns"
        :data="data"
        :showTable="viewMode"
      />

      <!-- 카드 형식 보기 (작은 화면) -->
      <div class="card" v-for="(item, index) in data" :key="index">
        <div class="card-header">
          <div class="d-flex justify-content-between align-items-center mb-2">
