<template>
  <div v-if="visible" class="modal-overlay">
    <div class="modal-container">
      <!-- 모달 헤더 -->
      <div class="modal-header">
        <h5 class="modal-title">실적 수정</h5>
        <button type="button" class="btn-close" @click="closeModal" />
      </div>

      <!-- 모달 바디 -->
      <div class="modal-body">
        <form @submit.prevent="submitEdit">
          <!-- 시작일, 종료일 -->
          <div class="d-flex">
            <div class="form-group w-50 me-2">
              <label for="editFromDate">시작일</label>
              <input
                type="date"
                class="form-control"
                id="editFromDate"
                v-model="editData.fromDate"
              />
            </div>
            <div class="form-group w-50">
              <label for="editToDate">종료일</label>
              <input
                type="date"
                class="form-control"
                id="editToDate"
                v-model="editData.toDate"
              />
            </div>
          </div>
          <br />
          <!-- 유형, 상태 -->
          <div class="d-flex">
            <div class="form-group me-2" style="flex: 4 3;">
              <label for="editWorkType">유형</label>
              <select class="form-control" id="editWorkType" v-model="editData.workType">
                <option>프로젝트</option>
                <option>회의</option>
                <option>보고서 작성</option>
              </select>
            </div>
            <div class="form-group" style="flex: 4 1;">
              <label for="editStatus">상태</label>
              <select class="form-control" id="editStatus" v-model="editData.status">
                <option>진행 중</option>
                <option>완료</option>
                <option>대기 중</option>
              </select>
            </div>
          </div>
          <br />
          <!-- 성과 주제 -->
          <div class="form-group">
            <label for="editPerformanceTitle">성과 주제</label>
            <input
              type="text"
              class="form-control"
              id="editPerformanceTitle"
              placeholder="성과 주제를 입력해주세요"
              v-model="editData.performanceTitle"
            />
          </div>
          <br />
          <!-- 성과 -->
          <div class="form-group">
            <label for="editPerformance">성과</label>
            <textarea
              class="form-control"
              id="editPerformance"
              placeholder="성과를 입력해주세요"
              v-model="editData.performance"
              rows="4"
            ></textarea>
          </div>
          <br />
          <!-- 푸터 -->
          <div class="modal-footer">
            <DefaultButton 
              align="right"
              color="gray"
              @click="closeModal"
            >
              취소
            </DefaultButton>
            <DefaultButton 
              align="right"
              color="red"
              @click="showDeleteAlert"
            >
              삭제
            </DefaultButton>
            <DefaultButton 
              align="right"
              @click="submitEdit"
            >
              수정
            </DefaultButton>
          </div>
        </form>
      </div>
    </div>
    <AlertModal 
      :isVisible="deleteConfirmVisible" 
      :disableBackgroundClose="true"
      title="삭제 확인" 
      confirmText="확인" 
      cancelText="취소"
      @close="deleteConfirmVisible = false" 
      @confirm="deleteNotice">
      <template #body>
        <p>삭제하시겠습니까?</p>
      </template>
    </AlertModal>
  </div>
</template>

<script setup>
import { ref, watch, defineProps, defineEmits } from 'vue';
import DefaultButton from '@/components/common/button/DefaultButton.vue';
import AlertModal from '@/components/common/modal/AlertModal.vue';

const props = defineProps({
  visible: { type: Boolean, default: false },
  editDataProp: {
    type: Object,
    default: () => ({
      fromDate: '',
      toDate: '',
      workType: '프로젝트',
      performanceTitle: '',
      performance: '',
      status: '진행 중',
    }),
  },
});
const emits = defineEmits(['close', 'save', 'delete']);

/**
 * 로컬 데이터
 */
const editData = ref({ ...props.editDataProp });

/**
 * 부모에서 editDataProp가 바뀌면 localEditData도 반영
 */
watch(
  () => props.editDataProp,
  (newVal) => {
    editData.value = { ...newVal };
  },
  { deep: true }
);

/** 모달 닫기 */
function closeModal() {
  emits('close');
}

/** 수정 */
function submitEdit() {
  /** 
   * 
   * 변경된 데이터 빈칸, 값 등 체크 로직 추가되야함
   * 
   */
  // 수정 완료 후 부모에게 알림
  emits('save', { ...editData.value });
}

const deleteConfirmVisible = ref(false);

/** 삭제 버튼 클릭 => AlertModal 열기 */
function showDeleteAlert() {
  deleteConfirmVisible.value = true;
}

/** AlertModal에서 확인 시 => 부모에 삭제 이벤트 알림 */
function deleteNotice() {
  // 현재 항목 ID를 부모에 전달
  deleteConfirmVisible.value = false;
  // (부모에서 AlertModal 등으로 재확인할 수도 있음)
  emits('delete', { ...editData.value });
}
</script>

<style scoped>
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.5);
  z-index: 1050;
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-container {
  background: #fff;
  width: 100%;
  max-width: 600px;
  border-radius: 4px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.5);
  animation: fadeIn 0.3s;
  padding: 1rem;
}

.modal-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 1rem;
}

.modal-body {
  max-height: 70vh;
  overflow-y: auto;
}
.modal-footer {
  display: flex;
  justify-content: flex-end;
  margin-top: 1rem;
}

/* 유사 부트스트랩 유틸 */
.d-flex {
  display: flex;
  gap: 0.5rem;
}
.me-2 {
  margin-right: 0.5rem;
}
.w-50 {
  width: 50%;
}
.form-control {
  width: 100%;
  padding: 0.4rem;
  margin: 0;
}

@media (max-width: 1200px) {
  .modal-container {
    width: 80% !important;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: scale(0.95);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}
</style>
