<!--
  ※ DefaultTable 컴포넌트 예시 및 프롭스 설명 ※

  예시 사용법:
  <DefaultTable
    :columns="[
      { key: 'name', label: '이름', width: 120 },
      { key: 'email', label: '이메일', width: 200 },
      { key: 'department', label: '부서', width: 150 },
      { key: 'team', label: '팀', width: 150 }
    ]"
    :data="userData"
    :footerData="footerData"
    :showTable="true"
    :noColumns="false"
    :rowClick="handleRowClick"
    :openSidebar="openSidebar"
    :bodyFontSize="'0.875rem'"
    :calculateAmountPerPerson="(item) => item.amount"
  />

  프롭스 설명:
  - columns: 테이블의 컬럼 정보 배열.
      각 컬럼 객체는 다음과 같은 속성을 가질 수 있음:
        - key: 데이터의 키 값.
        - label: 헤더에 표시할 텍스트.
        - width: 픽셀 단위의 고정 폭 (선택). 지정하지 않을 경우 기본값 150px 사용.
        - customClass: 특정 데이터에 적용할 클래스 지정 함수 (선택).
        - customValue: 데이터 표시를 커스터마이징하기 위한 함수 (선택).

  - data: 테이블에 표시할 데이터 배열. 각 객체는 columns의 key에 해당하는 값을 포함해야 함.
  - footerData: 테이블 하단에 표시할 푸터 데이터 배열 (옵션).
  - openSidebar: 행 클릭 시 사이드바를 열기 위한 함수 (옵션).
  - showTable: 테이블을 표시할지 여부 (true이면 표시).
  - rowClick: 테이블 행 클릭 시 실행할 콜백 함수 (옵션).
  - noColumns: 컬럼 헤더 표시 여부 (false이면 헤더를 표시).
  - bodyFontSize: 테이블 본문 폰트 사이즈 (CSS 단위, 기본값 '0.875rem').
  - calculateAmountPerPerson: 데이터 항목별 인원당 금액 계산 함수 (옵션, 기본값은 item.amount).
-->
<template>
  <div class="table-container">
    <table v-if="showTable" class="table" :style="{ '--table-body-font-size': bodyFontSize }">
      <thead v-if="!noColumns">
        <tr>
          <th v-for="column in columns" :key="column.key">
            {{ column.label }}
          </th>
        </tr>
      </thead>
      <tbody>
        <tr
          v-for="(item, rowIndex) in data"
          :key="rowIndex"
          @click="handleRowClick(item)"
        >
          <td
            v-for="column in columns"
            :key="column.key"
            :class="[ column.customClass ? column.customClass(item[column.key]) : '', 'ellipsis' ]"
            :style="columnStyles(column)"
          >
            {{ column.customValue ? column.customValue(item) : item[column.key] }}
          </td>
        </tr>
      </tbody>
      <tfoot>
        <tr>
          <td v-for="(footerItem, index) in footerData" :key="index">
            {{ footerItem }}
          </td>
        </tr>
      </tfoot>
    </table>
  </div>
</template>

<script setup>
import { defineProps } from 'vue';

const props = defineProps({
  // 테이블의 컬럼 정보 배열
  columns: {
    type: Array,
    required: false
  },
  // 테이블에 표시할 데이터 배열.
  // 각 객체는 컬럼 key에 대응하는 값을 포함함.
  data: {
    type: Array,
    required: true
  },
  // 테이블 하단 표시 푸터 데이터
  footerData: {
    type: Array,
    required: false,
    default: () => []
  },
  // 행 클릭 시 사이드바를 열기 위한 함수 (옵션)
  openSidebar: {
    type: Function,
    required: false
  },
  // 테이블을 표시할지 여부를 결정하는 값.
  // true면 테이블을 표시함.
  showTable: {
    type: Boolean,
    required: false,
    default: true
  },
  // 테이블 행 클릭 시 실행할 콜백 함수 (옵션)
  rowClick: {
    type: Function,
    required: false,
    default: null
  },
  // 컬럼 헤더 표시 여부.
  // false이면 컬럼 헤더를 표시함.
  noColumns: {
    type: Boolean,
    required: false,
    default: false
  },
  // 바디 폰트 사이즈
  bodyFontSize: {
    type: String,
    required: false,
    default: '0.875rem'
  },
  // 데이터 항목별 인원당 금액을 계산하는 함수 (옵션)
  calculateAmountPerPerson: {
    type: Function,
    required: false,
    default: (item) => item.amount
  },
});

const columnStyles = (column) => {
  const style = {};
  // 컬럼의 width가 지정되어 있으면 해당 폭을 사용하고, 없으면 기본값 150px 사용
  const fixedWidth = column.width || 150;
  style.width = fixedWidth + 'px';
  style.minWidth = fixedWidth + 'px';
  style.maxWidth = fixedWidth + 'px';
  
  return style;
};

const handleRowClick = (item) => {
  if (props.openSidebar) {
    props.openSidebar(item);
  }
  if (props.rowClick) {
    props.rowClick(item);
  }
};
</script>

<style scoped>
.table-container {
  width: 100%;
  overflow-x: auto;
}

.table {
  border-collapse: collapse;
  width: 100%;
}

/* th, td 기본 패딩 및 수평 경계선 */
.table th,
.table td {
  padding: 8px;
  /* border-top: 1px solid #ccc; */
  /* border-bottom: 1px solid #ccc; */
}

/* 첫 번째 셀에는 왼쪽 경계선 제거, 나머지 셀에는 왼쪽 경계선 (얇은 회색) 적용 */
.table th:not(:first-child),
.table td:not(:first-child) {
  border-left: 1px solid #e0e0e0;
}

/* th는 상하 경계선을 굵게 처리 (2px solid) */
.table th {
  border-top: 1px solid #a5a5a5;
  border-bottom: 1px solid #a5a5a5;
}

/* 항상 ellipsis 적용 (텍스트가 넘치면 ... 표시) */
.ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

/* 테이블 본문 폰트 사이즈 */
tbody {
  font-size: var(--table-body-font-size, 1rem);
}

/* 기본 텍스트 색상 (예시) */
.table-container table tbody td {
  color: rgb(27, 27, 27) !important;
}

/* 여러가지 커스텀 텍스트 색상 예시 */
.text-red {
  color: red;
  font-weight: bold;
}
.text-black {
  color: black;
}
.text-blue {
  color: blue;
}
.text-green {
  color: green;
}

/* 행 Hover 시 배경색 */
tbody tr:hover td {
  background-color: #e7f1ff !important;
}

@media (max-width: 1200px) {
  .table {
    font-size: 0.75rem !important;
  }
  .table-container table tbody td {
    font-size: 0.75rem;
  }
  .table th,
  .table td {
    padding: 6px;
  }
}

@media (max-width: 700px) {
  .table {
    font-size: 0.5rem !important;
  }
  .table-container table tbody td {
    font-size: 0.5rem;
  }
  .table th,
  .table td {
    padding: 5px;
  }
}
</style>
