<template>
  <!-- v-if="visible" 로 모달 표시/숨김 제어 -->
  <div v-if="visible" class="modal-overlay">
    <div class="modal-container">
      <!-- 모달 헤더 -->
      <div class="modal-header">
        <h5 class="modal-title">실적 추가</h5>
        <button type="button" class="btn-close" @click="closeModal" />
      </div>

      <!-- 모달 바디 -->
      <div class="modal-body">
        <form @submit.prevent="submitForm">
          <!-- 시작일, 종료일 -->
          <div class="d-flex">
            <div class="form-group w-50 me-2">
              <label for="fromDate">시작일</label>
              <input
                type="date"
                class="form-control"
                id="fromDate"
                v-model="localFormData.fromDate"
              />
            </div>
            <div class="form-group w-50">
              <label for="toDate">종료일</label>
              <input
                type="date"
                class="form-control"
                id="toDate"
                v-model="localFormData.toDate"
              />
            </div>
          </div>
          <br />
          <!-- 유형, 상태 -->
          <div class="d-flex">
            <div class="form-group me-2" style="flex: 4 3;">
              <label for="workType">유형</label>
              <select
                class="form-control"
                id="workType"
                v-model="localFormData.workType"
              >
                <option>프로젝트</option>
                <option>회의</option>
                <option>보고서 작성</option>
              </select>
            </div>
            <div class="form-group" style="flex: 4 1;">
              <label for="status">상태</label>
              <select
                class="form-control"
                id="status"
                v-model="localFormData.status"
              >
                <option>진행 중</option>
                <option>완료</option>
                <option>대기 중</option>
              </select>
            </div>
          </div>
          <br />
          <!-- 성과 주제 -->
          <div class="form-group">
            <label for="performanceTitle">성과 주제</label>
            <input
              type="text"
              class="form-control"
              id="performanceTitle"
              placeholder="성과 주제를 입력해주세요"
              v-model="localFormData.performanceTitle"
            />
          </div>
          <br />
          <!-- 성과 -->
          <div class="form-group">
            <label for="performance">성과</label>
            <textarea
              class="form-control"
              id="performance"
              placeholder="성과를 입력해주세요"
              v-model="localFormData.performance"
              rows="4"
            ></textarea>
          </div>
          <br />
          <!-- 버튼 영역 -->
          <div class="modal-footer">
            <DefaultButton 
              align="right"
              color="gray"
              @click="closeModal"
            >
              취소
            </DefaultButton>
            <DefaultButton 
              align="right"
              @click="submitForm"
            >
              저장
            </DefaultButton>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, watch, defineProps, defineEmits } from 'vue';
import DefaultButton from '@/components/common/button/DefaultButton.vue';

const props = defineProps({
  visible: { type: Boolean, default: false },
  formData: {
    type: Object,
    default: () => ({
      fromDate: '',
      toDate: '',
      workType: '프로젝트',
      performanceTitle: '',
      performance: '',
      status: '진행 중',
    }),
  },
});
const emits = defineEmits(['close', 'submit']);

/**
 * 모달 내부에서 쓸 지역 데이터
 * => 부모로부터 받은 formData를 로컬 복사
 */
const localFormData = ref({ ...props.formData });

/**
 * props.formData가 바뀔 때마다 localFormData를 다시 세팅
 */
watch(
  () => props.formData,
  (newVal) => {
    localFormData.value = { ...newVal };
  },
  { deep: true }
);

/** 모달 닫기 */
function closeModal() {
  emits('close');
}

/** 폼 전송(저장) */
function submitForm() {
  /** 
   * 
   * 변경된 데이터 빈칸, 값 등 체크 로직 추가되야함
   * 
   */
  // localFormData를 그대로 부모에게 emit
  emits('submit', { ...localFormData.value });
}
</script>

<style scoped>
/* 간단한 커스텀 모달 스타일 (Bootstrap JS 사용 안 함, so no 17px padding) */

/* 반투명 배경 */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.5);
  z-index: 1050;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* 모달 컨테이너 */
.modal-container {
  background: #fff;
  width: 100%;
  max-width: 600px;
  border-radius: 4px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.5);
  animation: fadeIn 0.3s;
  padding: 1rem;
}

/* 헤더 */
.modal-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 1rem;
}

/* 바디, 푸터 */
.modal-body {
  max-height: 70vh;
  overflow-y: auto;
}
.modal-footer {
  display: flex;
  justify-content: flex-end;
  margin-top: 1rem;
}

/* 기존 .d-flex, .form-control, .me-2 등 Bootstrap 스타일 일부만 mimic */
.d-flex {
  display: flex;
  gap: 0.5rem;
}
.w-50 {
  width: 50%;
}
.form-control {
  width: 100%;
  padding: 0.4rem;
  margin: 0;
}
.me-2 {
  margin-right: 0.5rem;
}

@media (max-width: 1200px) {
  .modal-container {
    width: 80% !important;
  }
}

/* 간단한 fadeIn 애니메이션 */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: scale(0.95);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}
</style>
