    margin-bottom: 10px;
  }
  .search-controls {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    margin-top: 30px;
  }
  .search-controls > div {
    width: 100%;
    display: flex;
    justify-content: flex-end;
  }
  /* 이름 그룹을 위쪽에 배치 */
  .name-group {
    order: -1;
