<template>
  <!-- b-modal: 자식 컴포넌트에서 상태를 v-model로 받아 제어 -->
  <!-- b-modal에서 “OK” 버튼 누르면 자동으로 모달이 닫히는 게 디폴트 동작 -->
  <!-- @ok.prevent="onSaveClick"(수동 제어) 형태 -->
  <!-- onSaveClick() 내부에서 직접 innerModalVisible.value = false;를 호출하지 않는 한 모달이 안 닫힘 -->
  <b-modal
    v-model="innerModalVisible"
    title="사용자 세부 정보"
    @ok.prevent="onSaveClick"
    ok-title="저장"
    cancel-title="취소"
    no-close-on-backdrop
    centered
  >
    <b-form @change="handleChange">
      <!-- 이름, 생년월일 -->
      <b-form-group label="이름/생년월일" label-for="userDetails" class="user-detail-group">
        <div id="userDetails" class="d-flex">
          <b-form-input
            v-model="tempUser.name"
            :disabled="true"
            placeholder="이름"
            class="mr-2"
          ></b-form-input>
          <b-form-input
            id="userBirthDate"
            type="date"
            v-model="tempUser.birthDate"
            :disabled="true"
          ></b-form-input>
        </div>
      </b-form-group>

      <!-- 이메일 -->
      <b-form-group label="이메일" label-for="userEmail" class="user-detail-group">
        <div id="userEmail" class="d-flex">
          <b-form-input
            v-model="tempUser.email"
            placeholder="이메일"
            class="mr-2"
          ></b-form-input>
        </div>
      </b-form-group>

      <!-- 사용자 비밀번호 강제 초기화 -->
      <b-form-group label="비밀번호/비밀번호 확인" label-for="userPassword" class="user-detail-group">
        <div id="userPassword" class="d-flex">
          <b-form-input
            v-model="tempUser.password"
            placeholder="비밀번호"
            class="mr-2"
          ></b-form-input>
          <b-form-input
            v-model="passwordCheck"
            placeholder="비밀번호 확인"
            class="mr-2"
          ></b-form-input>
        </div>
        <!-- 비밀번호 불일치 경고 메시지 -->
        <p v-if="isPasswordMismatch" class="userPasswordNotMatch">비밀번호와 재확인 값이 일치하지 않습니다.</p>
      </b-form-group>

      <!-- 고용형태, 입사일/퇴사일 -->
      <b-form-group label="고용형태/입사일/퇴사일" label-for="employmentDetails" class="user-detail-group">
        <div id="employmentDetails" class="d-flex">
          <b-form-select
            v-model="tempUser.employmentType"
            :options="['정규직', '비정규직']"
            :disabled="!isAdmin"
            class="mr-2"
          ></b-form-select>
          <b-form-select
            v-model="tempUser.status"
            :options="['입사', '퇴사']"
            class="mr-2"
          ></b-form-select>
          <b-form-input
            type="date"
            v-model="computedDate"
            :disabled="true"
          ></b-form-input>
        </div>
      </b-form-group>

      <!-- 부서, 팀, 직책 한 줄 -->
      <b-form-group label="부서/팀/직책" label-for="userEmploymentDetails" class="user-detail-group">
        <div id="userEmploymentDetails" class="d-flex">
          <b-form-input
            v-model="tempUser.department"
            :disabled="!isAdmin"
            placeholder="부서"
            class="mr-2"
          ></b-form-input>
          <b-form-input
            v-model="tempUser.team"
            :disabled="!isAdmin"
            placeholder="팀"
            class="mr-2"
          ></b-form-input>
          <b-form-input
            v-model="tempUser.position"
            :disabled="!isAdmin"
            placeholder="직책"
          ></b-form-input>
        </div>
      </b-form-group>

      <!-- 우편번호 및 주소 -->
      <b-form-group label="우편번호/주소" label-for="userZipCode" class="user-detail-group">
        <div id="userAddress" class="d-flex">
          <b-form-input
            v-model="tempUser.zipCode"
            :disabled="true"
            placeholder="우편번호"
            class="mr-2"
            style="width: 33%"
          ></b-form-input>
        </div>
        <b-form-input
          v-model="tempUser.address"
          :disabled="true"
          placeholder="주소"
          style="width: 100%"
        ></b-form-input>
      </b-form-group>

      <!-- 현재 파견지 -->
      <b-form-group label="현재 파견지" label-for="userLocation" class="user-detail-group">
        <b-form-input
          id="userLocation"
          v-model="tempUser.location"
          :disabled="!isAdmin"
          placeholder="현재 파견지"
        ></b-form-input>
      </b-form-group>
    </b-form>
  </b-modal>
</template>

<script setup>
import { ref, computed, watch } from 'vue';

const props = defineProps({
  visible: { type: Boolean, default: false },
  isAdmin: { type: Boolean, default: false },
  user: {
    type: Object,
    default: () => ({
      name: '',
      email: '',
      password: '',
      department: '',
      team: '',
      position: '',
      joinDate: '',
      leaveDate: '',
      status: '입사',
      birthDate: '',
      employmentType: '정규직',
      zipCode: '',
      address: '',
      location: ''
    })
  }
});

const emits = defineEmits(['close', 'confirmSave']);

// b-modal 제어를 위한 내부 ref
const innerModalVisible = ref(props.visible);

// 모달 안에서 편집할 임시 데이터
const tempUser = ref({ ...props.user });

// 비밀번호 재확인
const passwordCheck = ref(props.user.password);
const isPasswordMismatch = ref(false);

/**
 * 모달 열리고 닫힘 동기화
 */
watch(
  () => props.visible,
  (newVal) => {
    innerModalVisible.value = newVal;
    // 모달 열릴 때마다 tempUser 갱신
    if (newVal) {
      tempUser.value = { ...props.user };
      passwordCheck.value = props.user.password;
      isPasswordMismatch.value = false;
    }
  }
);

watch(
  () => innerModalVisible.value,
  (newVal) => {
    // 자식 모달이 닫혔다면, 부모에게 close 이벤트
    if (!newVal) {
      emits('close');
    }
  }
);

/**
 * 입사/퇴사 날짜를 표시하는 계산 속성
 */
const computedDate = computed({
  get() {
    return tempUser.value.status === '입사'
      ? tempUser.value.joinDate
      : tempUser.value.leaveDate;
  },
  set(value) {
    if (tempUser.value.status === '입사') {
      tempUser.value.joinDate = value;
    } else {
      tempUser.value.leaveDate = value;
    }
  }
});

/**
 * 폼 변경 이벤트
 */
function handleChange() {
  // 비밀번호 불일치 여부
  isPasswordMismatch.value = (tempUser.value.password !== passwordCheck.value);
}

/**
 * 저장 버튼 (b-modal의 @ok) 클릭 시
 */
function onSaveClick() {
  // 비밀번호 불일치면 바로 알림
  if (isPasswordMismatch.value) {
    // 부모에게 알려주기
    emits('confirmSave', {
      isMismatch: true,
      isChanged: false,
      userData: tempUser.value,
      statusChangedFlag: false
    });
    return;
  }

  // 원본과 비교해서 변경 여부 체크
  const original = JSON.stringify(props.user);
  const current = JSON.stringify(tempUser.value);
  const isChanged = (original !== current);

  // 상태 변경 여부 (입사/퇴사)
  const statusChangedFlag = (tempUser.value.status !== props.user.status);

  // 부모에게 저장 확인 이벤트
  emits('confirmSave', {
    isMismatch: false,
    isChanged,
    userData: tempUser.value,
    statusChangedFlag
  });
}
</script>

<style scoped>
/* 모달 전체 배경, 다이얼로그, 내용 등에 대한 CSS */
/* (BootstrapVue의 .modal-* 클래스와 겹칠 수 있으므로 필요 없으면 제거해도 됩니다.) */

/* 비밀번호 불일치 경고 */
.userPasswordNotMatch {
  color: red;
}
/* 폼 그룹(한 줄 표현) */
.user-detail-group {
  margin-bottom: 10px !important;
}
/* 우편번호 그룹 */
#userAddress {
  margin-bottom: 10px;
}

/* 반응형 모달 등은 BootstrapVue가 기본적으로 지원하므로
   직접 .modal, .modal-dialog, .modal-content 등을 커스텀할 필요가 적습니다.

   필요하다면 아래처럼 커스텀할 수 있음 (예시)
   .modal-dialog {
     max-width: 400px;
   }
*/
</style>
