<template>
  <div>
    <!-- 메인 콘텐츠 영역 -->
    <div class="content" :class="{ 'content-expanded': sidebarVisible && !isMobile }">
      <h2 class="content-title">권범준님 영수증 신청내역</h2>
      <div class="content-sub-title">
        <p>날짜: [{{ filteredStartDate }} ~ {{ filteredEndDate }}]</p>
      </div>
      
      <!-- 뒤로가기 버튼 -->
      <DefaultButton 
        size="small" 
        align="left"
        color="gray"
        @click="goBack">
        뒤로가기
      </DefaultButton>

      <div class="d-flex align-items-center content-body-header">
        <span class="mr-2 all-click">전체 승인</span>
        <!-- <input 
          type="radio" 
          name="approveAll" 
          id="approveAll" 
          @change="toggleApproveAll" 
          v-model="selectAllDecision"
          value="approve" 
          class="mr-4" 
        /> -->
        <DefaultTextfield
          type="radio"
          name="approveAll" 
          id="approveAll"
          @change="toggleApproveAll" 
          v-model="selectAllDecision"
          value="approve" 
          size="small"
        />
        <span class="mr-2 all-click">전체 반려</span>
        <!-- <input 
          type="radio" 
          name="rejectAll" 
          id="rejectAll" 
          @change="toggleRejectAll" 
          v-model="selectAllDecision"
          value="reject" 
        /> -->
        <DefaultTextfield
          type="radio"
          name="rejectAll" 
          id="rejectAll"
          @change="toggleRejectAll" 
          v-model="selectAllDecision"
          value="reject" 
          size="small"
        />
      </div>
      <hr />

      <!-- 테이블 보기 (큰 화면에서만 보임) -->
      <table class="table mt-3" id="dataTable" v-if="!isMobile">
        <thead>
          <tr>
            <th>발생일</th>
            <th>인원</th>
            <th>구분</th>
            <th>사유</th>
            <th>금액</th>
            <th>영수증 사진</th>
            <th>승인</th>
            <th>반려</th>
            <th>반려사유</th>
          </tr>
        </thead>
        <tbody>
          <tr
            v-for="(item, index) in data"
            :key="index"
            @click="openSidebar(item)"
          >
            <td>{{ item.date }}</td>
            <td>{{ item.people.length }}</td>
            <td>{{ item.type }}</td>
            <td>{{ item.reason }}</td>
            <td>{{ formatAmount(item.amount) }}원</td>
            <td>
              <a
                href="#"
                @click.prevent="openPreviewModal(item.receipt)"
                class="text-primary"
              >
                {{ item.receiptName }}
              </a>
            </td>
            <td>
              <input
                type="radio"
                :name="'decision' + index"
                class="approveRadio"
                v-model="item.decision"
                value="approve"
                @change="updateDecision()"
              />
            </td>
            <td>
              <input
                type="radio"
                :name="'decision' + index"
                class="rejectRadio"
                v-model="item.decision"
                value="reject"
                @change="updateDecision()"
              />
            </td>
            <td>
              <!-- <input
                type="text"
                class="form-control rejectReason"
                v-model="item.rejectReason"
              /> -->
              <DefaultTextfield
                size="full"
                v-model="item.rejectReason"
              />
            </td>
          </tr>
        </tbody>
      </table>

      <!-- 카드 레이아웃 보기 (작은 화면에서만 보임) -->
      <div class="card-layout" v-if="isMobile">
        <div
          class="card"
          v-for="(item, index) in data"
          :key="index"
        >
          <div class="card-header">
            <p class="card-title">{{ item.date }}</p>
          </div>
          <div class="card-body">
            <p class="card-text">
              <strong>인원 : </strong>
              <span @click="togglePeopleList(item)" style="cursor: pointer;">
                {{ item.people.length }}명
              </span>
            </p>
            <div v-if="showPeopleList(item)">
              <div class="people-list">
                <div class="people-list-header">명단</div>
                <p
                  v-for="person in item.people"
                  :key="person.name"
                  class="people-list-item"
                >
                  {{ person.name }} ({{ person.department }} - {{ person.team }})
                </p>
              </div>
              <hr />
            </div>
            <p class="card-text">
              <strong>구분/사유 : </strong>
              {{ item.type }} / {{ item.reason }}
            </p>
            <p class="card-text">
              <strong>금액 : </strong>
              {{ formatAmount(item.amount) }}
            </p>
            <p class="card-text">
              <strong>금액/인원수 : </strong>
              {{ calculateAmountPerPerson(item) }}
            </p>
            <p>
              <strong class="card-text">영수증 사진 : </strong>
              <a
                @click.prevent="openPreviewModal(item.receipt)"
                style="cursor: pointer; color: blue;"
              >
                {{ item.receiptName }}
              </a>
            </p>
            <p class="card-text">
              <strong>결정: </strong>
              승인
              <input
                type="radio"
                :name="'decision' + index"
                class="approveRadio"
                v-model="item.decision"
                value="approve"
                @change="updateDecision()"
              />
              | 반려
              <input
                type="radio"
                :name="'decision' + index"
                class="rejectRadio"
                v-model="item.decision"
                value="reject"
                @change="updateDecision()"
              />
              <br>
            </p>
            <p class="card-text d-flex align-items-center">
              <strong>반려사유: </strong>
              <input
                type="text"
                class="form-control rejectReason ml-2"
                v-model="item.rejectReason"
                style="flex: 1;"
              />
            </p>
          </div>
        </div>
      </div>

      <!-- 저장 버튼 -->
      <DefaultButton align="right" @click="save">
        저장
      </DefaultButton>

      <!-- 페이지네이션 (공통 컴포넌트) -->
      <DefaultPagination
        :currentPage="currentPage"
        :totalPages="totalPages"
        :visiblePageCount="visiblePageCount"
        @pageChange="onPageChange"
      />

      <!-- 사이드바 추가 -->
      <div :class="['sidebar-wrapper', { 'sidebar-visible': sidebarVisible }]">
        <ReceiptSidebar 
          v-if="!isMobile" 
          :item="selectedItem" 
          :visible="sidebarVisible" 
          @close="closeSidebar"
          @preview-image="openPreviewModal"
        />
      </div>
      
      <!-- 이미지 미리보기 모달 -->
      <div
        v-if="isPreviewVisible"
        class="modal preview-modal"
        @click="closePreviewModalOnOutsideClick"
      >
        <div
          class="preview-modal-content"
          @mousedown="startDrag"
          @mousemove="onDrag"
          @mouseup="endDrag"
          @mouseleave="endDrag"
          @touchstart="startDrag"
          @touchmove="onDrag"
          @touchend="endDrag"
        >
          <img
            :src="previewImage"
            :class="{ zoomed: isZoomed }"
            class="preview-modal-image"
            :style="{
              transform: isZoomed
                ? `translate(${zoomedPosition.x}px, ${zoomedPosition.y}px) scale(1.5)`
                : 'none',
              transformOrigin: `${zoomOrigin.x}px ${zoomOrigin.y}px`
            }"
            @dblclick="toggleZoom"
            @touchstart="toggleZoom"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted } from 'vue';
import { useRouter } from 'vue-router';
import ReceiptSidebar from './ReceiptSidebar.vue';
import DefaultButton from '@/components/common/button/DefaultButton.vue';
import DefaultPagination from '@/components/common/pagination/DefaultPagination.vue';
import DefaultTextfield from '@/components/common/textfield/DefaultTextfield.vue';
// import axios from 'axios'; // 실제 백엔드 호출 시

import { usePreviewModal } from '@/utils/preview-modal';
const {
  isPreviewVisible,
  previewImage,
  isZoomed,
  zoomedPosition,
  zoomOrigin,
  openPreviewModal,
  toggleZoom,
  startDrag,
  onDrag,
  endDrag
} = usePreviewModal();

// 사이드바 / 반응형
const selectedItem = ref(null);
const sidebarVisible = ref(false);
const isMobile = ref(window.innerWidth <= 1400);
function updateIsMobile() {
  isMobile.value = window.innerWidth <= 1400;
}

// 날짜 표시 (필터 기간)
const filteredStartDate = ref('2024-01');
const filteredEndDate = ref('2024-03');

// 뒤로가기
const router = useRouter();
function goBack() {
  router.back();
}

// 실제 서버에서 받아올 데이터
const data = ref([]);

// 페이지네이션
const currentPage = ref(1);
const totalPages = ref(1);
const visiblePageCount = ref(5);  // 페이지네이션에서 보여줄 최대 페이지 버튼 수

/**
 * 서버에서 데이터 가져오기
 * - 실제로는 axios 호출 + 필요한 파라미터(page 등) 전송
 * - 응답 받은 data와 totalPages를 세팅
 */
// async function fetchDataFromServer(page = 1) {
async function fetchDataFromServer() {
  try {
    // const response = await axios.get('/api/some-endpoint', {
    //   params: { page },
    // });
    // data.value = response.data.items;
    // totalPages.value = response.data.totalPages;

    // 여기서는 예시(mockData)
    const mockData = [
      {
        date: '2024-08-01',
        type: '식비',
        reason: '야근',
        amount: '100,000',
        receiptName: '영수증 예시',
        receipt: 'https://kwon-beom-jun.github.io/centrally-system-page/img/receipt_example.jpg',
        rejectionReason: '',
        people: [
          { department: 'SI사업본부', team: '프레임워크', name: '권범준', position: '책임' },
          { department: 'SI사업본부', team: '프레임워크', name: '홍길동', position: '선임' },
        ],
      },
      {
        date: '2024-08-02',
        type: '식비',
        reason: '야근',
        amount: '110,000',
        receiptName: '영수증 예시',
        receipt: 'https://kwon-beom-jun.github.io/centrally-system-page/img/receipt_example.jpg',
        rejectionReason: '',
        people: [
          { department: 'SI사업본부', team: '프레임워크', name: '권범준', position: '책임' },
          { department: 'SI사업본부', team: '프레임워크', name: '홍길동', position: '선임' },
        ],
      },
      // ... 원하는 만큼
    ];
    // mock 응답 처리
    data.value = mockData.map(item => ({
      ...item,
      decision: '', // 승인/반려 라디오 기본값
    }));

    totalPages.value = 3; // 예시로 총 3페이지 있다고 가정
  } catch (err) {
    console.error(err);
  }
}

/**
 * 페이지 변경 시(페이지네이션 컴포넌트로부터 이벤트)
 * - 백엔드 재요청
 */
function onPageChange(newPage) {
  currentPage.value = newPage;
  fetchDataFromServer(newPage);
}

// 저장 버튼 클릭
function save() {
  console.log('Data saved:', data.value);
  // 서버로 data.value 전송하는 로직 추가 가능
}

// --------------------------------------------------------------------------------------
// 승인/반려 (전체 선택 or 개별 선택) 로직
// --------------------------------------------------------------------------------------
const selectAllDecision = ref(''); // 전체 승인/반려 라디오
function toggleApproveAll() {
  if (selectAllDecision.value === 'approve') {
    data.value.forEach(item => { item.decision = 'approve'; });
  } else {
    data.value.forEach(item => { item.decision = ''; });
  }
}
function toggleRejectAll() {
  if (selectAllDecision.value === 'reject') {
    data.value.forEach(item => { item.decision = 'reject'; });
  } else {
    data.value.forEach(item => { item.decision = ''; });
  }
}
function updateDecision() {
  const allApproved = data.value.length > 0 && data.value.every(item => item.decision === 'approve');
  const allRejected = data.value.length > 0 && data.value.every(item => item.decision === 'reject');

  if (allApproved) {
    selectAllDecision.value = 'approve';
  } else if (allRejected) {
    selectAllDecision.value = 'reject';
  } else {
    selectAllDecision.value = '';
  }
}

// --------------------------------------------------------------------------------------
// 기타 로직
// --------------------------------------------------------------------------------------
function openSidebar(item) {
  if (!isMobile.value) {
    selectedItem.value = item;
    sidebarVisible.value = true;
  }
}
function closeSidebar() {
  sidebarVisible.value = false;
}
function closePreviewModalOnOutsideClick(event) {
  if (!event.target.classList.contains('preview-modal-image')) {
    isPreviewVisible.value = false;
  }
}

// 금액 포맷팅
function formatAmount(amountStr) {
  const amount = parseInt(amountStr.replace(/[^0-9]/g, ''), 10) || 0;
  return amount.toLocaleString();
}

// 인원수별 나눠기
function calculateAmountPerPerson(item) {
  const amountNum = parseInt(item.amount.replace(/[^0-9]/g, ''), 10);
  const peopleCount = item.people.length || 0;
  return peopleCount > 0
    ? Math.floor(amountNum / peopleCount).toLocaleString() + ' 원'
    : 'N/A';
}

// 모바일에서 인원 목록 토글
const visiblePeopleLists = ref({});
function togglePeopleList(item) {
  if (visiblePeopleLists.value[item.date]) {
    delete visiblePeopleLists.value[item.date];
  } else {
    visiblePeopleLists.value[item.date] = true;
  }
}
function showPeopleList(item) {
  return visiblePeopleLists.value[item.date];
}

// --------------------------------------------------------------------------------------
// 초기화
// --------------------------------------------------------------------------------------
onMounted(() => {
  // 첫 페이지 데이터 불러오기
  fetchDataFromServer(currentPage.value);

  // 반응형 체크
  window.addEventListener('resize', updateIsMobile);
});
</script>

<style scoped>
#dataTable tbody tr:hover td {
  background-color: #e7f1ff !important;
}

.content {
  transition: margin-right 0.3s ease;
}

.content-sub-title {
  margin-bottom: 0px !important;
}

.content-expanded {
  margin-right: 300px;
}

.content-body-header {
  margin-top: 60px;
}

.text-primary {
  color: #c7573e;
  /* 원하는 하이퍼링크 색상으로 변경 */
  text-decoration: none;
  /* 하이퍼링크 밑줄 제거 */
}

/* 사이드바 */
.sidebar-wrapper {
  position: fixed;
  right: 0;
  top: 0;
  width: 300px;
  height: 100%;
  transform: translateX(100%);
  transition: transform 0.3s ease;
  z-index: 1000;
}

/* 사이드바가 보일 때 적용되는 클래스 */
.sidebar-visible {
  transform: translateX(0);
  /* 화면 안으로 슬라이드 */
}

/* 사이드바 내용물 스타일 유지 */
.sidebar {
  width: 100%;
  height: 100%;
  background-color: #f9f9f9;
  border-left: 1px solid #ddd;
  padding: 20px;
  box-shadow: -2px 0 5px rgba(0, 0, 0, 0.1);
  overflow-y: auto;
}

/* 전체 승인/반려 */
.all-click {
  font-weight: bold;
  margin-left: 10px;
}

/* 카드 레이아웃 인원 목록 스타일 추가 */
.people-list {
  font-size: 0.7rem;
  max-height: 150px;
  overflow-y: auto;
  margin-top: 10px;
  border-top: 1px solid #ddd;
  padding-top: 10px;
}
.people-list-header {
  font-weight: bold;
  margin-bottom: 5px;
}
.people-list-item {
  padding: 1px 0;
  margin: 0;
  margin-left: 10px;
}

/* 이미지 미리보기 모달 */
.preview-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.7);
  z-index: 9999;
  display: flex;
  align-items: center;
  justify-content: center;
}
.preview-modal-content {
  position: relative;
}
.preview-modal-image {
  max-width: 80vw;
  max-height: 80vh;
  transition: transform 0.2s ease;
}

@media (max-width: 1400px) {
  .page-link {
    font-size: 0.75rem;
    padding: 0.3rem 0.6rem;
  }

  .page-item {
    font-size: 0.75rem;
  }

  .pagination {
    margin: 3px 0 60px 0;
  }
}

@media (max-width: 1200px) {
  .bnt-back {
    margin-bottom: 40px;
  }

  .rejectReason {
    width: 100%;
  }
}
</style>
