<template>
  <div>
    <!-- 메인 콘텐츠 영역 -->
    <div class="content">
      <h2 class="content-title">공지 사항</h2>
      <div class="content-sub-title">
        <div class="text-muted">Notice Management</div>
      </div>

      <div class="create-notice">
        <!-- "공지등록" 버튼 (isAdmin 일 때만 표시) -->
        <DefaultButton
          v-if="isAdmin"
          align="right"
          @click="showModal('create')"
        >
          공지등록
        </DefaultButton>
      </div>

      <!-- 테이블 보기 -->
      <!-- 로우 클릭 시 showModal('view', item) -->
      <DefaultTable
        :columns="columns"
        :data="data"
        :rowClick="(item) => showModal('view', item)"
      />

      <!-- 페이지네이션 (공통 컴포넌트) -->
      <DefaultPagination
        :currentPage="currentPage"
        :totalPages="totalPages"
        :visiblePageCount="visiblePageCount"
        @pageChange="onPageChange"
      />
    </div>

    <!-- 모달 컴포넌트 -->
    <NoticeIntegratedCreateModal
      v-if="isModalVisible"
      :isAdmin="isAdmin"
      :isCreate="isCreate"
      :form="form"
      @close="isModalVisible = false"
      @save="handleSave"
      @delete="handleDelete"
    />
  </div>
</template>

<script setup>
import { ref, onMounted } from 'vue';
import NoticeIntegratedCreateModal from '@/components/hrm/NoticeIntegratedCreateModifyModal.vue';
import DefaultButton from '@/components/common/button/DefaultButton.vue';
import DefaultTable from '@/components/common/table/DefaultTable.vue';
import DefaultPagination from '@/components/common/pagination/DefaultPagination.vue';
import NoticeIntegratedApi from '@/api/hrm/NoticeIntegratedApi';

// =====================
// 관리자 여부
// =====================
const isAdmin = ref(true);

// =====================
// 모달 표시/숨김 & 등록/수정 구분
// =====================
const isModalVisible = ref(false);
const isCreate = ref(true);

// =====================
// 페이지네이션 상태
// =====================
const currentPage = ref(1);
const totalPages = ref(1);          // 서버에서 받아온 총 페이지 수
const visiblePageCount = ref(5);    // 페이지네이션에서 보여줄 최대 페이지 버튼 수

// =====================
// 폼 데이터
// =====================
const form = ref({
  id: null,
  title: '',
  author: '',
  date: '',
  content: '',
  cookieConsent: false,
});

// =====================
// 실제 서버에서 받아올 데이터
// =====================
const data = ref([]);

// =====================
// 컬럼 정의
// =====================
const columns = [
  { key: 'id',     label: '번호'  , width: 10 },
  { key: 'title',  label: '제목'  , width: 200 },
  { key: 'author', label: '작성자', width: 30 },
  { key: 'date',   label: '작성일', width: 30 },
];

/**
 * 페이지 변경 시 호출
 * -> 서버(또는 mock)에서 해당 페이지 데이터, totalPages 등을 받아옴
 */
function onPageChange(newPage) {
  currentPage.value = newPage;
  fetchDataFromServer(newPage);
}

/**
 * 백엔드(또는 mock)에서 데이터 가져오는 함수
 * - 실제로는 axios.get(...) 등을 사용하여 { page } 등의 파라미터 전송
 */
// async function fetchDataFromServer(page = 1) {
async function fetchDataFromServer() {
  console.log('test');
  try {
    // const response = await NoticeIntegratedApi.getNotices('/api/notice', { params: { page } });
    const response = await NoticeIntegratedApi.getNotices();
    data.value = response.data.items;
    totalPages.value = response.data.totalPages;

    // =====================
    // 예시: mockData
    // =====================
    const mockData = [
      { id: 1,  title: '공지사항 제목 1',  author: '관리자A', date: '2024-08-01', content: '공지사항 내용 1' },
      { id: 2,  title: '공지사항 제목 2',  author: '관리자B', date: '2024-08-02', content: '공지사항 내용 2' },
      { id: 3,  title: '공지사항 제목 3',  author: '관리자C', date: '2024-08-03', content: '공지사항 내용 3' },
      { id: 4,  title: '공지사항 제목 4',  author: '관리자A', date: '2024-08-04', content: '공지사항 내용 4' },
      { id: 5,  title: '공지사항 제목 5',  author: '관리자D', date: '2024-08-05', content: '공지사항 내용 5' },
      { id: 6,  title: '공지사항 제목 6',  author: '관리자E', date: '2024-08-06', content: '공지사항 내용 6' },
      { id: 7,  title: '공지사항 제목 7',  author: '관리자F', date: '2024-08-07', content: '공지사항 내용 7' },
      { id: 8,  title: '공지사항 제목 8',  author: '관리자B', date: '2024-08-08', content: '공지사항 내용 8' },
      { id: 9,  title: '공지사항 제목 9',  author: '관리자C', date: '2024-08-09', content: '공지사항 내용 9' },
      { id: 10, title: '공지사항 제목 10', author: '관리자A', date: '2024-08-10', content: '공지사항 내용 10' },
      // 실제로는 페이지 번호에 따라 다른 데이터가 내려올 수도 있음
    ];
    // 모의로 현재 페이지 데이터 & totalPages 세팅
    data.value = mockData;
    totalPages.value = 3;  // 예: 총 3페이지로 가정
  } catch (error) {
    console.error(error);
  }
}

// =====================
// 모달 로직
// =====================
function showModal(mode, item = null) {
  isCreate.value = (mode === 'create');
  if (isCreate.value) {
    // 등록 모드 (새로운 데이터)
    form.value = {
      id: null,
      title: '',
      author: '',
      date: getLocalDate(),
      content: '',
      cookieConsent: false,
    };
  } else {
    // 수정 모드
    form.value = { ...item };
  }
  isModalVisible.value = true;
}

function getLocalDate() {
  const today = new Date();
  today.setMinutes(today.getMinutes() - today.getTimezoneOffset());
  return today.toISOString().split('T')[0];
}

/**
 * 저장(등록/수정)
 * - 실제로는 백엔드에 POST/PUT 요청 후, 다시 fetchDataFromServer()로 목록을 재조회하는 식
 */
function handleSave(updatedForm) {
  if (isCreate.value) {
    // 등록
    updatedForm.id = Date.now(); // 임시 ID
    data.value.push(updatedForm);
  } else {
    // 수정
    const idx = data.value.findIndex(d => d.id === updatedForm.id);
    if (idx !== -1) {
      data.value[idx] = updatedForm;
    }
  }
  // 모달 닫기
  isModalVisible.value = false;
  fetchDataFromServer();
}

/**
 * 삭제
 * - 실제로는 백엔드에 DELETE 요청 후 다시 fetchDataFromServer()로 목록 재조회
 */
function handleDelete(idToDelete) {
  if (idToDelete !== null) {
    data.value = data.value.filter(item => item.id !== idToDelete);
  }
  isModalVisible.value = false;
  fetchDataFromServer();
}

/**
 * onMounted 시 1페이지 데이터를 불러옴
 */
onMounted(() => {
  fetchDataFromServer(currentPage.value);
});
</script>

<style scoped>
/* 페이지네이션 margin */
.pagination {
  margin-top: 30px !important;
}

/* 테이블 행 hover 색상 */
#dataTable tbody tr:hover td {
  background-color: #e7f1ff !important;
}

.create-notice {
  margin-bottom: 20px;
}
</style>
