<template>
  <!-- <div
      v-if="isVisible"
      class="modal fade" 
      id="receiptModal" 
      tabindex="-1" 
      role="dialog" 
      aria-labelledby="receiptModalLabel" 
      aria-hidden="true"
      :class="{ show: isVisible }"
      :style="{ display: isVisible ? 'block' : 'none' }"
    > -->
  <div class="modal fade" id="receiptModal" tabindex="-1" role="dialog" aria-hidden="true">
    <div class="modal-dialog modal-dialog-centered" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="receiptModalLabel">영수증 등록</h5>
        </div>
        <div class="modal-body">
          <form @submit.prevent="openConfirmationModal">

            <!-- 이미지 등록 -->
            <div class="form-group">
              <label for="receiptImage">영수증 사진</label>
              <div class="mt-2">
                <input type="file" class="form-control-file" id="receiptImage" @change="handleFileChange" />
              </div>
            </div>

            <!-- 영수증 발행일 -->
            <div class="form-group">
              <label for="date">영수증 발행일</label>
              <input type="date" class="form-control" id="date" v-model="formData.date" />
            </div>

            <!-- 구분 | 금액 -->
            <div class="form-row align-items-center">
              <div class="form-group col">
                <label for="type">구분</label>
                <select class="form-control" id="type" v-model="formData.type">
                  <option>식비</option>
                  <option>교통비</option>
                  <option>회식</option>
                  <option>기타</option>
                </select>
              </div>
              <div class="form-group col">
                <label for="amount">금액</label>
                <div class="input-group">
                  <input type="text" class="form-control" id="amount" placeholder="내용을 입력해주세요" v-model="formData.amount" />
                  <div class="input-group-append">
                    <span class="input-group-text">원</span>
                  </div>
                </div>
              </div>
            </div>

            <!-- 사용자 검색 필드 -->
            <div class="form-group">
              <label for="userSearch">사용자 검색 <p style="font-size: 0.8rem; color: red;">테스트 '권범','홍길동','이영희'</p>
              </label>
              <input type="text" class="form-control" id="userSearch" placeholder="사용자 이름 검색" v-model="userSearch"
                @input="searchUsers" @keydown.enter.prevent />
              <!-- 검색 결과 목록 -->
              <ul v-if="searchResults.length > 0" class="list-group mt-2">
                <li v-for="(user, index) in searchResults" :key="index" class="list-group-item"
                  @click="selectUser(user)">
                  {{ user.name }} ({{ user.department }} - {{ user.team }} - {{ user.position }})
                </li>
              </ul>
            </div>

            <!-- 추가된 인원 목록 -->
            <div v-if="formData.people.length > 0" class="mt-3 form-group">
              <label>추가된 인원 목록</label>
              <ul class="list-group">
                <li v-for="(person, index) in formData.people" :key="index" class="list-group-item">
                  {{ person.name }} - {{ person.department }} - {{ person.team }} - {{ person.position }}
                </li>
                <!--
                
                
                  추가인원 삭제 버튼 필요


                -->
              </ul>
            </div>


            <!-- 사유 -->
            <div class="form-group reason">
              <label for="reason">사유</label>
              <input type="text" class="form-control" id="reason" placeholder="내용을 입력해주세요" v-model="formData.reason" />
            </div>


            <div class="modal-footer">
              <button type="button" class="btn btn-secondary" @click="$emit('close')">취소</button>
              <button type="submit" class="btn btn-primary">등록</button>
            </div>
          </form>
        </div>
      </div>
    </div>

    <AlertModal 
      :isVisible="confirmationModalVisible" 
      :disableBackgroundClose="true"
      title="저장 확인" 
      confirmText="확인" 
      cancelText="취소"
      @close="confirmationModalVisible = false" 
      @confirm="submitReceipt">
      <template #body>
        <p>등록하시겠습니까?</p>
      </template>
    </AlertModal>
  </div>
</template>

<script setup>
import { ref, defineProps } from 'vue';
import AlertModal from '@/components/common/modal/AlertModal.vue';
// import * as bootstrap from 'bootstrap';

defineProps({
  isVisible: Boolean
});

const formData = ref({
  receiptImage: null,
  date: '',
  type: '식비',
  amount: '',
  reason: '',
  people: []
});

const userSearch = ref('');
const searchResults = ref([]);

const handleFileChange = (event) => {
  formData.value.receiptImage = event.target.files[0];
};

// const removePerson = (index) => {
//   formData.value.people.splice(index, 1);
// };

// 사용자 검색 함수
const searchUsers = async () => {
  if (userSearch.value.length < 2) {
    searchResults.value = [];
    return;
  }

  // try {
  //   const response = await axios.get('/api/users', {
  //     params: { query: userSearch.value }
  //   });
  //   searchResults.value = response.data;
  // } catch (error) {
  //   console.error('사용자 검색 오류:', error);
  // }

  // ---------------------------------------- 예시 데이터 ----------------------------------------
  // 예시 데이터 사용
  const exampleUsers = [
    { name: '권범준', department: 'SI사업본부', team: '프레임워크', position: '책임' },
    { name: '권범선', department: 'SI사업본부', team: '프레임워크', position: '책임' },
    { name: '권범이', department: 'SI사업본부', team: '프레임워크', position: '책임' },
    { name: '권범삼', department: 'SI사업본부', team: '프레임워크', position: '책임' },
    { name: '홍길동', department: 'SI사업본부', team: '프레임워크', position: '선임' },
    { name: '이영희', department: 'SI사업본부', team: '프레임워크', position: '사원' }
  ];
  // 검색어에 맞는 예시 데이터를 필터링
  searchResults.value = exampleUsers.filter(user =>
    user.name.includes(userSearch.value)
  );
  // --------------------------------------------------------------------------------------------

};

const selectUser = (user) => {
  /* 
  
    ※ 추가 인원 중복 체크가 있어야함 ※

  */
  formData.value.people.push(user);
  userSearch.value = '';
  searchResults.value = [];
};

const confirmationModalVisible = ref(false);

const openConfirmationModal = () => {
  confirmationModalVisible.value = true;
};

const submitReceipt = () => {
  console.log('Receipt Submitted:', formData.value);
  
  // 예시로 axios 요청 추가
  // axios.post('/api/submit-receipt', formData.value)
  //   .then(response => {
  //     console.log(response.data);
  //     // 추가 성공 시 알림 등 처리
  //   })
  //   .catch(error => {
  //     console.error('Error submitting receipt:', error);
  //   });

  // 제출 후 초기화
  formData.value = {
    receiptImage: null,
    date: '',
    type: '식비',
    amount: '',
    reason: '',
    people: []
  };
  userSearch.value = '';
  confirmationModalVisible.value = false; // 모달 닫기
  emit('close'); // 부모 컴포넌트 알림
};

const emit = defineEmits(['close', 'confirm']);

// const openModal = () => {
//   // formData, userSearch, searchResults 초기화
//   formData.value = {
//     receiptImage: null,
//     date: '',
//     type: '식비',
//     amount: '',
//     reason: '',
//     people: []
//   };
//   userSearch.value = '';  // 사용자 검색 입력 초기화
//   searchResults.value = [];  // 사용자 검색 결과 초기화

//   // 파일 입력 필드 초기화
//   const fileInput = document.getElementById('receiptImage');
//   if (fileInput) {
//     fileInput.value = '';  // 파일 입력 필드 값 초기화
//   }

//   const modal = document.getElementById('receiptModal');
//   if (!modalInstance.value) {
//     modalInstance.value = new bootstrap.Modal(modal, {
//       backdrop: 'static', // 배경 클릭 시 모달이 닫히지 않도록 설정
//       keyboard: false, // ESC 키로 모달이 닫히지 않도록 설정
//     });
//   }
//   modalInstance.value.show();
// };
</script>

<style scoped>
.modal-header {
  display: flex;
  justify-content: space-between;
  font-size: 1.5rem;
}

.reason {
  margin-top: 10px;
}

.form-group {
  margin-bottom: 20px;
}

.form-row {
  display: flex;
  gap: 10px;
}

.list-group-item {
  font-size: 0.7rem;
}

@media (max-width: 1200px) {
  .form-group {
    margin-bottom: 10px;
  }

  .form-group .mt-2 {
    margin: 0 !important;
  }

  .form-group label {
    font-size: 0.9rem;
  }
