<template>
  <div class="container mt-5">
    <div class="row justify-content-center">
      <div class="col-md-8">
        <div class="card">
          <div class="card-header text-center">
            <img src="https://www.kpcnc.co.kr/src/img/logo.png" class="logo mb-3" alt="logo">
          </div>
          <div class="card-body">
            <!--
              렌더링 후 참조 설정
                Vue는 컴포넌트가 마운트될 때 ref 속성이 붙은 DOM 요소를 찾아서, 
                해당 요소를 지정한 ref 변수(여기서는 formRef)에 할당
                이때 formRef.value는 실제 <form> DOM 요소가 됨

              DOM 요소의 내장 메서드 활용
                HTML <form> 요소는 기본적으로 checkValidity()나 reportValidity() 같은
                내장 메서드를 가지고 있음.
                formRef.value를 통해 이 메서드들을 호출할 수 있게 되는 것
            -->
            <form ref="formRef" @submit.prevent="handleSubmit">

              <!-- 이름과 핸드폰 번호 -->
              <div class="form-group custom-grid">
                <div>
                  <label for="userName">이름</label>
                  <DefaultTextfield
                    id="userName"
                    v-model="userName"
                    size="full"
                    required
                  />
                </div>
                <div>
                  <label for="phone">핸드폰 번호</label>
                  <DefaultTextfield
                    id="phone"
                    v-model="phone"
                    size="full"
                    placeholder="'-' 없이 입력하세요"
                    required
                  />
                </div>
              </div>

              <!-- 이메일 + 인증코드 / 인증 메일 발송 -->
              <div class="form-group">
                <div>
                  <label for="email">이메일</label>
                  <div style="display: flex; align-items: center;">
                    <DefaultTextfield
                      id="email"
                      v-model="email"
                      size="full"
                      validationType="email"
                      required
                    />
                    <DefaultButton
                      color="gray"
                      @click="sendEmailVerification"
                      size="large"
                    >
                      인증 메일 발송
                    </DefaultButton>
                  </div>
                </div>
              </div>
              <!-- 인증코드 입력 + 버튼 (한 줄 배치) -->
              <div class="form-group">
                <div style="display: flex; align-items: center;">
                  <DefaultTextfield
                    id="verificationCode"
                    v-model="verificationCode"
                    size="small"
                    :disabled="verificationCodeDisable"
                    placeholder="인증코드 입력"
                  />
                  <DefaultButton
                    color="yellow"
                    @click="sameVerification"
                    size="small"
                  >
                    인증 확인
                  </DefaultButton>
                </div>
              </div>

              <!-- 비밀번호와 비밀번호 확인 -->
              <div class="form-group custom-grid mt-3">
                <div>
                  <label for="password">비밀번호</label>
                  <!-- <input type="password" class="form-control" id="password" v-model="password" required> -->
                  <DefaultTextfield
                    type="password"
                    id="password"
                    v-model="password"
                    size="full"
                    required
                  />
                </div>
                <div>
                  <label for="confirmPassword">비밀번호 확인</label>
                  <DefaultTextfield
                    type="password"
                    id="confirmPassword"
                    v-model="confirmPassword"
                    size="full"
                    required
                  />
                </div>
              </div>

              <!-- 생년월일과 입사일 -->
              <div class="form-group custom-grid mt-3">
                <div>
                  <label for="dob">생년월일</label>
                  <DefaultTextfield
                    type="date"
                    id="dob"
                    v-model="dob"
                    size="full"
                    required
                  />
                </div>
                <div>
                  <label for="joinDate">입사일</label>
                  <DefaultTextfield
                    type="date"
                    id="joinDate"
                    v-model="joinDate"
                    size="full"
                    required
                  />
                </div>
              </div>

              <!-- 부서, 팀, 직급 -->
              <div class="form-group custom-grid-3 mt-3">
                <div>
                  <label for="department">부서</label>
                  <select class="form-control" id="department" v-model="department" required>
                    <option value="" disabled>부서 선택</option>
                    <option value="IT">IT</option>
                    <option value="HR">HR</option>
                    <option value="Finance">Finance</option>
                  </select>
                </div>
                <div>
                  <label for="team">팀</label>
                  <select class="form-control" id="team" v-model="team" required>
                    <option value="" disabled>팀 선택</option>
                    <option value="Team A">Team A</option>
                    <option value="Team B">Team B</option>
                    <option value="Team C">Team C</option>
                  </select>
                </div>
                <div>
                  <label for="position">직급</label>
                  <DefaultTextfield
                    id="position"
                    v-model="position"
                    size="full"
                    placeholder="직급 입력"
                    required
                  />
                </div>
              </div>

              <!-- 우편번호와 주소 -->
              <div class="form-group mt-3">
                <label for="zonecode">우편번호</label>
                <div class="input-group">
                  <DefaultTextfield
                    id="zonecode"
                    v-model="zonecode"
                    size="full"
                    placeholder="우편번호"
                    :disabled="true"
                    required
                  />
                  <div class="input-group-append">
                    <DefaultButton
                      color="gray"
                      @click="openModal"
                      size="large"
                    >
                      주소 검색
                    </DefaultButton>
                  </div>
                </div>
              </div>

              <div class="form-group mt-3">
                <label for="address">주소</label>
                <DefaultTextfield
                  id="address"
                  v-model="address"
                  size="full"
                  placeholder="주소를 입력하세요"
                  :disabled="true"
                  required
                />
              </div>

              <!-- 상세주소 입력 -->
              <div class="form-group mt-3">
                <label for="detailAddress">상세 주소</label>
                <DefaultTextfield
                  id="detailAddress"
                  v-model="detailAddress"
                  size="full"
                  placeholder="상세 주소를 입력하세요"
                />
              </div>

              <!-- 저장 버튼 -->
              <div class="form-group">
                <div class="btn-container">
                  <DefaultButton
                    size="large"
                    @click="handleSubmit">
                    가입하기
                  </DefaultButton>
                  <DefaultButton
                    size="large"
                    color="gray"
                    @click="onGoBack">
                    돌아가기
                  </DefaultButton>
                </div>
              </div>

            </form>
          </div>
        </div>
      </div>
    </div>
    <KakaoAddressModal
      :visible="isModalOpen"
      @close="closeModal"
      @selectAddress="handleSelectAddress"
    />
  </div>
</template>

<script setup>
import { ref } from 'vue';
import { useRouter } from 'vue-router';
import DefaultButton from '@/components/common/button/DefaultButton.vue';
import DefaultTextfield from '@/components/common/textfield/DefaultTextfield.vue';
import KakaoAddressModal from '@/components/common/kakao/address/KakaoAddressModal.vue';

// 라우터 사용 설정
const router = useRouter();

const userName = ref('');
const email = ref('');
const phone = ref('');
const dob = ref('');
const joinDate = ref('');
const department = ref('');
const team = ref('');  // 팀을 위한 상태 추가
const position = ref('');
const password = ref('');
const confirmPassword = ref('');

// 인증 코드 입력값
const verificationCode = ref('');
const verificationCodeDisable = ref(false);

/**
 * 카카오 주소 API
 */
const zonecode = ref('');
const address = ref('');
const detailAddress = ref('');
const isModalOpen = ref(false);

function openModal() {
  isModalOpen.value = true;
}
function closeModal() {
  isModalOpen.value = false;
}
function handleSelectAddress(payload) {
  // payload = { address: ..., zonecode: ... }
  address.value = payload.address;
  zonecode.value = payload.zonecode;
}

/**
 * 인증 메일 발송 버튼 클릭 시
 *  - 백엔드에 email 보내서 인증코드 생성 & 메일 발송 요청
 */
async function sendEmailVerification() {
  if (!email.value) {
    alert('이메일을 입력하세요.');
    return;
  }

  try {
    // 백엔드 API 예시 (POST /api/auth/sendVerificationCode)
    // 실제 로직은 프로젝트 API 경로에 맞춰 수정
    const response = await fetch('/api/auth/sendVerificationCode', {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({ email: email.value })
    });
    if (response.ok) {
      alert('인증 메일이 발송되었습니다. 메일함을 확인하세요!');
    } else {
      alert('인증 메일 발송에 실패했습니다.');
    }
  } catch (error) {
    console.error('인증 메일 발송 에러:', error);
  }
}

/**
 * 인증 확인 버튼 클릭 시
 *  - 백엔드에 인증코드 보내서 검증
 */
async function sameVerification() {
  try {
    // 백엔드 API 예시 (POST /api/auth/sendVerificationCode)
    // 실제 로직은 프로젝트 API 경로에 맞춰 수정
    const response = await fetch('/api/auth/sendVerificationCode', {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({ email: email.value })
    });
    if (response.ok) {
      verificationCodeDisable.value = true;
    } else {
      alert('인증 코드 검증에 실패했습니다.');
    }
  } catch (error) {
    console.error('인증 코드 검증 에러:', error);
  }
}

/**
 * 회원 가입 벡엔드 호출
 *  - 폼 제출 시 호출되는 함수
 */
const formRef = ref(null);
const handleSubmit = () => {
  // formRef를 통해 폼 요소의 유효성을 검사합니다.
  if (!formRef.value.checkValidity()) {
    formRef.value.reportValidity();
    return;
  }

  if (password.value !== confirmPassword.value) {
    alert('비밀번호가 일치하지 않습니다.');
    return;
  }

  if (!verificationCode.value) {
    alert('인증코드를 입력하세요.');
    return;
  }

/**
 * 
 * 
 * 
 * 
 *  verificationCode는 백엔드에서 암호화 된 값으로 해줘야함
 *  이후 암호화 된 값을 백엔드에서 검증해야함
 * 
 * 
 * 
 * 
 */
  // 서버로 데이터를 전송하는 로직을 추가하세요
  console.log('회원가입 완료:', {
    userName: userName.value,
    email : email.value,
    phone: phone.value,
    dob: dob.value,
    joinDate: joinDate.value,
    department: department.value,
    team: team.value, // 팀 정보 추가
    position: position.value,
    address: address.value,
    detailAddress: detailAddress.value,
    password: password.value,
    verificationCode: verificationCode.value
  });
};

const onGoBack = () => {
  router.push('login');
};
</script>

<style scoped>
.container {
  margin-top: 50px;
}

.logo {
  width: 150px;
  margin: 10px 0 10px 0 !important;
}

.card-header {
  background-color: #f8f9fa;
  border-bottom: 1px solid #dee2e6;
}

.form-group {
  margin-bottom: 20px;
}

.btn-container {
  display: flex; /* 버튼을 가로로 정렬 */
  justify-content: center; /* 자식 요소들을 가로로 중앙 정렬 */
  gap: 0.5rem; /* 버튼 사이의 간격 설정 */
  width: 100%; /* 부모 요소의 너비에 맞게 */
}

/* Custom Grid Layout */
.custom-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px; /* 필드 간의 간격 */
}

#zonecode {
  max-width: 30%; /* 우편번호 필드의 너비를 50%로 설정 */
}

/* 부서, 팀, 직급을 한 줄에 3개 배치 */
.custom-grid-3 {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px; /* 필드 간의 간격 */
}

/* 모바일에서는 두 개의 필드가 한 줄에 나타나도록 강제 */
@media (max-width: 1200px) {
  label {
    font-size: 0.7rem !important;
  }
  .custom-grid {
    grid-template-columns: repeat(2, 1fr); /* 필드를 두 개씩 한 줄에 나타내도록 강제 */
  }
  .form-group {
    margin-top: 10px !important;
    margin-bottom: 0px !important;
  }
  .btn-save-back {
    margin-top: 20px !important;
  }
  .form-control {
    font-size: 0.65rem !important;
  }
  .btn-container {
    margin-bottom: 20px;
  }
}
</style>
