<template>
  <div>
    <!-- 메인 콘텐츠 영역 -->
    <div class="content">
      <h2 class="content-title">이력 관리</h2>
      <div class="content-sub-title">
        <p>사용자 이력을 관리 페이지 입니다</p>
      </div>

      <div class="search-controls">
        <!-- 이름 검색 그룹 -->
        <DefaultFormRow align="right">
          <DefaultLabel text="이름(이메일) :" forId="searchData" size="small" />
          <DefaultTextfield
            type="text"
            id="searchData"
            v-model="searchData"
            size="small"
            placeholder="이름(이메일) 입력"
          />
          <DefaultButton 
            @click="search"
            size="small">
            조회
          </DefaultButton>
        </DefaultFormRow>
        <!-- 시작, 종료일 그룹 -->
        <DefaultFormRow align="right">
          <DefaultLabel text="시작 :" forId="startDate" size="small" />
          <DefaultTextfield
            type="month"
            id="startDate"
            v-model="startDate"
            size="xsmall"
          />
          <div class="gap-right" />
          <DefaultLabel text="종료 :" forId="endDate" size="small" />
          <DefaultTextfield
            type="month"
            id="endDate"
            v-model="endDate"
            size="xsmall"
          />
        </DefaultFormRow>
      </div>

      <div class="activity-log-table">
        <!-- DefaultTable 컴포넌트 (큰 화면) -->
        <DefaultTable
          :columns="columns"
          :data="data"
          :showTable="viewMode"
          :bodyFontSize="'0.7rem'"
        />
      </div>
      
      <!-- 페이지네이션 (공통 컴포넌트) -->
      <DefaultPagination
        :currentPage="currentPage"
        :totalPages="totalPages"
        :visiblePageCount="visiblePageCount"
        @pageChange="onPageChange"
      />
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted } from 'vue';
import DefaultTable from '@/components/common/table/DefaultTable.vue';
import DefaultButton from '@/components/common/button/DefaultButton.vue';
import DefaultPagination from '@/components/common/pagination/DefaultPagination.vue';
import DefaultTextfield from '@/components/common/textfield/DefaultTextfield.vue';
import DefaultLabel from '@/components/common/label/DefaultLabel.vue';
import DefaultFormRow from '@/components/common/DefaultFormRow.vue';
import ActivityLogApi from '@/api/system/ActivityLogApi';


// import axios from 'axios'; // 실제 백엔드 호출 시 사용

// ================ 상태 정의 ================
const today = new Date().toISOString().slice(0, 7);
const startDate = ref(today);
const endDate = ref(today);
const searchData = ref('');

// 실제 서버에서 받아온 데이터
const data = ref([]);

// 페이지네이션
const currentPage = ref(1);
const totalPages = ref(1);
const visiblePageCount = ref(5);  // 페이지네이션에서 보여줄 최대 페이지 버튼 수

// 컬럼 정의
const columns = ref([
  { key: 'username', label: '이름', width: 50 },
  { key: 'userEmail', label: '이메일', width: 130 },
  { key: 'menu', label: '메뉴', width: 100 },
  { key: 'function', label: '기능', width: 120 },
  { key: 'ip', label: 'IP(법적문제확인)', width: 120 },
  { key: 'httpMethod', label: 'METHOD', width: 40 },
  { key: 'httpBody', label: '본문', width: 120 },
  { key: 'timestamp', label: '시간', width: 120 },
]);

/**
 * 서버에서 데이터 가져오기
 * 실제로는 axios 등을 사용하여 파라미터(startDate, endDate, searchData, currentPage 등)를 전송 후,
 * 응답에서 data, totalPages 등을 받아 세팅하시면 됩니다.
 */
// async function fetchDataFromServer(currentPage = 1) {
async function fetchDataFromServer() {
  try {
    console.log(startDate.value);
    console.log(endDate.value);
    console.log(searchData.value);
    console.log(currentPage.value);
    const response = await ActivityLogApi.getActivityLogs(
      {
        startDate: startDate.value,
        endDate: endDate.value,
        searchData: searchData.value,
        currentPage: currentPage.value,
      }, 
    );
    // const response = await axios.get('/api/searchReceipts', {
    //   params: {
    //     startDate: startDate.value,
    //     endDate: endDate.value,
    //     searchData: searchData.value,
    //     currentPage: currentPage.vlaue,
    //   },
    // });
    data.value = response.data.content;
    totalPages.value = response.data.totalPages;

    // 여기서는 mockData로 예시 구현
    // const mockData = [
    //   {
    //     username: '시스템',
    //     userEmail: 'system',
    //     menu: '로그인',
    //     function: '로그인',
    //     ip: "X.X.1.1",
    //     httpMethod: "POST",
    //     httpBody: '{"username":"test","password":"1234"}',
    //     timestamp: "2025-03-03 12:12:48",
    //   },
    //   {
    //     username: '시스템',
    //     userEmail: 'system',
    //     menu: '로그인',
    //     function: '로그인',
    //     ip: "X.X.1.1",
    //     httpMethod: "POST",
    //     httpBody: '{"username":"test","password":"1234"}',
    //     timestamp: "2025-03-03 12:12:48",
    //   },
    //   {
    //     username: '시스템',
    //     userEmail: 'system',
    //     menu: '로그인',
    //     function: '로그인',
    //     ip: "X.X.1.1",
    //     httpMethod: "POST",
    //     httpBody: '{"username":"test","password":"1234"}',
    //     timestamp: "2025-03-03 12:12:48",
    //   },
    // ];
    
    // data.value = mockData;
    // totalPages.value = 2; // 예시로 총 2페이지 있다고 가정
  } catch (error) {
    console.error(error);
  }
}

/**
 * 검색 버튼 클릭 시 실행
 * - 검색 파라미터를 포함하여 페이지 1번으로 재조회
 */
function search() {
  currentPage.value = 1;
  fetchDataFromServer(currentPage.value);
}

/**
 * 페이지 변경 시 (DefaultPagination에서 방출)
 * - 새 페이지를 받아서 currentPage를 갱신
 * - 백엔드 재조회
 */
function onPageChange(newPage) {
  currentPage.value = newPage;
  fetchDataFromServer(currentPage.value);
}

onMounted(() => {
  // 컴포넌트 초기 로드시 첫 페이지 조회
  fetchDataFromServer(currentPage.value);
});
</script>

<style scoped>
#dataTable tbody tr:hover td {
  background-color: #e7f1ff !important;
}

.activity-log-table {
  margin-bottom: 10px;
}
.gap-right {
  margin-right: 10px;
}

/* 상태별 색상 설정 */
.status-complete {
  color: blue; /* 완료 상태는 파란색 */
}
.status-in-progress {
  color: red; /* 진행중 상태는 빨간색 */
}
.align-items-center {
  margin-bottom: 0px !important;
}
.start-end-date-group label {
  margin-left: 10px !important;
}
.bnt-search {
  margin: 0 !important;
}

.search-controls {
  margin-bottom: 20px;
}

@media (min-width: 1151px) {
}

@media (min-width: 1920px) {
}

@media (max-width: 1200px) {
