<template>
  <div class="content">
    <h2 class="content-title">프로젝트별 권한 부여</h2>
    <p class="content-sub-title">각 프로젝트별 사용자 권한을 관리하세요.</p>

    <!-- 부서, 팀, 프로젝트 선택 -->
    <div class="grid-container">
      <div class="grid-item">
        <label for="departmentSelect" class="form-label">부서 선택</label>
        <!-- <select id="departmentSelect" v-model="selectedDepartment" class="form-select">
          <option value="전체">전체</option>
          <option value="SI 사업본부">SI 사업본부</option>
          <option value="AI 사업본부">AI 사업본부</option>
          <option value="경영관리부">경영관리부</option>
        </select> -->
        <DefaultSelect
          id="departmentSelect"
          v-model="selectedDepartment"
          :options="departmentsOptions"
          placeholder="부서 선택"
          @change="updateTeams"
          size="full"
        />
      </div>

      <div class="grid-item">
        <label for="teamSelect" class="form-label">팀 선택</label>
        <!-- <select id="teamSelect" v-model="selectedTeam" class="form-select">
          <option v-for="team in teams" :key="team" :value="team">{{ team }}</option>
        </select> -->
        <DefaultSelect
          id="teamSelect"
          v-model="selectedTeam"
          :options="teamsOptions"
          placeholder="팀 선택"
          size="full"
        />
      </div>

      <div class="grid-item">
        <label for="projectSelect" class="form-label">프로젝트 선택</label>
        <!-- <select id="projectSelect" v-model="selectedProject" class="form-select">
          <option v-for="project in projects" :key="project" :value="project">{{ project }}</option>
        </select> -->
        <DefaultSelect
          id="projectSelect"
          v-model="selectedProject"
          :options="projectsOptions"
          placeholder="프로젝트 선택"
          size="full"
        />
      </div>

      <!-- 검색 입력 + 검색 버튼 -->
      <div class="grid-item">
        <label for="userSearch" class="form-label">사용자 검색</label>
        <div class="search-item-search">
          <DefaultTextfield
            type="text"
            id="userSearch"
            v-model="searchTerm"
            size="full"
            placeholder="이름(이메일) 입력"
          />
          <DefaultButton
            align="right"
            @click="fetchUsersFromServer"
            size="large">
            검색
          </DefaultButton>
        </div>
      </div>
    </div>

    <!-- 권한 테이블 (검색 버튼을 눌렀을 때만 표시됨) -->
    <div v-if="isTableVisible" class="table-section">
      <table class="table table-bordered">
        <thead class="table-head">
          <tr>
            <th>사용자</th>
            <th>프로젝트</th>
            <th>부서</th>
            <th>팀</th>
            <th>권한</th>
          </tr>
        </thead>
        <tbody class="table-body">
          <tr v-for="user in paginatedUsers" :key="user.id">
            <td>{{ user.name }}</td>
            <td>{{ user.project }}</td>
            <td>{{ user.department }}</td>
            <td>{{ user.team }}</td>
            <td>
              <select 
                v-model="user.permission"
                class="form-select permission-select"
                @change="onPermissionChange(user)"
              >
                <option :value="null" disabled>없음</option>
                <option
                  v-for="permission in getPermissionsFor(user.project)"
                  :key="permission"
                  :value="permission"
                >
                  {{ permission }}
                </option>
              </select>
            </td>
          </tr>
        </tbody>
      </table>

      <DefaultButton
        size="small"
        align="right"
        @click="savePermissions"
      >
        저장
      </DefaultButton>

      <!-- 페이지네이션 컴포넌트 -->
      <DefaultPagination
        :currentPage="currentPage"
        :totalPages="totalPages"
        :visiblePageCount="5"
        @pageChange="onPageChange"
      />
    </div>
  </div>
</template>

<script setup>
import { ref, watch, computed } from 'vue';
import DefaultButton from '@/components/common/button/DefaultButton.vue';
import DefaultPagination from '@/components/common/pagination/DefaultPagination.vue';
import DefaultSelect from '@/components/common/select/DefaultSelect.vue';
import DefaultTextfield from '@/components/common/textfield/DefaultTextfield.vue';
// import axios from 'axios'; // 실제 백엔드 사용 시

/**
 * 프로젝트 목록
 */
const projects = ref(['전체', '영수증 관리', '개인정보 관리']);

/**
 * 부서 & 팀 목록
 */
const departmentTeams = {
  '전체': ['전체'],
  'SI 사업본부': ['전체', '프레임워크 팀'],
  'AI 사업본부': ['전체', 'RPA', '클라우드', '빅데이터'],
  '경영관리부': ['전체', '국내 영업팀', '해외 영업팀']
};

/**
 * 권한 레벨
 */
const permissionLevels = {
  '영수증 관리': ['신청자', '승인자', '관리자'],
  '개인정보 관리': ['사용자', '관리자']
};

/**
 * 사용자 목록 (백엔드에서 가져올 데이터)
 */
const paginatedUsers = ref([]);

/**
 * 테이블 표시 여부 (검색 버튼 클릭 시 true)
 */
const isTableVisible = ref(false);

/**
 * 선택 필터 상태
 */
const selectedProject = ref('전체');
const selectedDepartment = ref('전체');
const selectedTeam = ref('전체');
const searchTerm = ref('');

/**
 * 페이지네이션 상태
 */
const currentPage = ref(1);
const totalPages = ref(1);
// const itemsPerPage = ref(5); 추후 주석 제거

/**
 * 사용자 권한 변경 기록용
 */
const changes = ref({}); 
/* 예: changes.value = {
  1: "관리자",    // ID=1번 사용자의 최종 권한
  7: "승인자",     // ID=7번 사용자의 최종 권한
}
*/

/**
 * 사용자 권한 변경
 */
function onPermissionChange(user) {
  // 사용자가 Select로 권한 변경했을 때 
  // changes 객체에 기록
  changes.value[user.id] = user.permission;
}

/**
 * 부서 선택 변경 시 팀 목록 리셋
 */
const teams = ref(departmentTeams['전체']);
watch(selectedDepartment, (newDept) => {
  teams.value = departmentTeams[newDept] || ['전체'];
  selectedTeam.value = '전체';
});

/**
 * 부서 & 팀 더미 옵션 데이터
 */
const departmentsOptions = ref([
  { value: '전체', label: '전체' },
  { value: '미지정', label: '미지정' },
  { value: 'SI 사업본부', label: 'SI 사업본부' },
  { value: 'AI 사업본부', label: 'AI 사업본부' },
  { value: '경영관리부', label: '경영관리부' }
]);
/* const teamsOptions = ref([
  { value: '', label: '팀 선택' },
  { value: '영업', label: '영업' },
  { value: '프레임워크 팀', label: '프레임워크 팀' },
  { value: 'RPA', label: 'RPA' },
  { value: '클라우드', label: '클라우드' },
  { value: '빅데이터', label: '빅데이터' },
  { value: '국내 영업팀', label: '국내 영업팀' },
  { value: '해외 영업팀', label: '해외 영업팀' }
]); */
/**
 * 팀 옵션: 원래는 teams(ref, 문자열 배열)을 사용하지만,
 * DefaultSelect는 객체 배열을 요구하므로, 이를 변환하는 computed 속성을 추가합니다.
 */
const teamsOptions = computed(() =>
  teams.value.map(team => ({ value: team, label: team }))
);

/**
 * 프로젝트 옵션: projects (문자열 배열)을 객체 배열로 변환
 */
const projectsOptions = computed(() =>
  projects.value.map(project => ({ value: project, label: project }))
);

/**
 * 프로젝트별 권한 목록
 */
function getPermissionsFor(project) {
  return permissionLevels[project] || [];
}

/**
 * 백엔드에서 사용자 데이터 가져오기 (검색 버튼 클릭 시 실행)
 */
const fetchUsersFromServer = async () => {
  // isTableVisible을 true로 설정하여 테이블 표시
  isTableVisible.value = true;

  // 백엔드 요청 (현재 주석 처리)
  /*
  try {
    const response = await axios.get('/api/users', {
      params: {
        project: selectedProject.value,
        department: selectedDepartment.value,
        team: selectedTeam.value,
        search: searchTerm.value,
        page: currentPage.value,
        limit: itemsPerPage.value,
      }
    });

    paginatedUsers.value = response.data.items;
    totalPages.value = response.data.totalPages;
  } catch (error) {
    console.error('데이터 가져오기 실패:', error);
  }
  */

  // 더미 데이터
  totalPages.value = 3;
  paginatedUsers.value = [
    { id: 1, name: '홍길동', department: 'SI 사업본부', team: '프레임워크 팀', project: '영수증 관리', permission: null },
    { id: 2, name: '이순신', department: 'AI 사업본부', team: '빅데이터', project: '개인정보 관리', permission: '관리자' },
    { id: 3, name: '강감찬', department: '경영관리부', team: '국내 영업팀', project: '영수증 관리', permission: '신청자' },
  ];

  // 수정 목록 초기화
  changes.value = {};
};

/**
 * 페이지 이동 이벤트 (DefaultPagination → @pageChange)
 * - newPage에 맞춰 currentPage 갱신 후 재조회
 */
function onPageChange(newPage) {
  currentPage.value = newPage;
  fetchUsersFromServer();
}

/**
 * 저장 (권한 업데이트)
 */
function savePermissions() {
  // changes 객체에 기록된 ID별 최종 권한만 추출
  // 예: { 1: '관리자', 7: '승인자' }
  // 여기서만 서버로 보내기
  console.log('변경된 사용자', changes.value);

  // 실제 서버 전송 (예시)
  /*
  axios.post('/api/update-permissions', changes.value)
    .then(...)
    .catch(...);
  */

  // 만약 저장 성공 후 changes 초기화
  // changes.value = {};
}
</script>

<style scoped>
.grid-container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
}

.grid-item {
  display: flex;
  flex-direction: column;
}

.search-item {
  display: flex;
  align-items: center;
}

.search-item input {
  flex: 1; /* 입력 필드가 남은 공간을 모두 차지하도록 설정 */
}

.search-item button {
  white-space: nowrap; /* 버튼 텍스트가 줄바꿈되지 않도록 설정 */
}

.search-item-search {
  display: flex;
  align-items: center;
}

/* DefaultTextfield 컴포넌트의 최상위 래퍼(.default-textfield)가 있다면 flex-grow를 적용 */
.search-item-search > .default-textfield {
  flex: 1;
  min-width: 0;
}

.form-control, .form-select {
  width: 100%;
}

.table-section {
  margin-top: 30px;
  width: 100%;
}

.permission-select {
  font-size: 0.75rem;
  height: 25px;
  padding: 0px 0px 0px 10px;
}

.table {
  width: 100%;
}

.table th, 
.table td {
  text-align: left;
  height: 30px !important;
  line-height: 30px !important;
  vertical-align: middle !important; /* 수직 정렬 중앙 */
  padding-top: 0 !important; 
  padding-bottom: 0 !important;
}

.text-end {
  text-align: right;
}

.save-button {
  margin-top: 0px !important;
  margin-bottom: 50px;
}

@media (max-width: 1200px) {
  .table-head {
    font-size: 0.65rem !important;
  }
  .table-body {
    font-size: 0.6rem !important;
  }
  .form-select {
    font-size: 0.75rem !important;
  }
  .permission-select {
    font-size: 0.6rem !important;
  }
  .grid-container {
    grid-template-columns: repeat(2, 1fr);
  }
  .table {
    font-size: 0.75rem;
  }
}
</style>
