<template>
  <div>
    <!-- 메인 콘텐츠 영역 -->
    <div class="content" :class="{ 'content-expanded': sidebarVisible && !isMobile }">
      <h2 class="content-title">영수증 등록</h2>
      <p class="content-sub-title">영수증 등록 페이지</p>

      <div class="create-receipt">
        <DefaultButton 
          align="right"
          @click="openModal">
          영수증 등록
        </DefaultButton>
      </div>

      <!-- 영수증 등록 모달 -->
      <ReceiptSubmissionCreateModal @close="closeModal" />

      <!-- 테이블 보기 -->
      <DefaultTable
        :columns="columns"
        :data="data"
        :openSidebar="openSidebar"
        :showTable="!isMobile"
      />

      <!-- 카드 레이아웃 (모바일에서만) -->
      <div class="card-layout" v-if="isMobile">
        <div class="card" v-for="(item, idx) in data" :key="idx">
          <div class="card-header">
            <p class="card-title">{{ item.date }}</p>
          </div>
          <div class="card-body">
            <p class="card-text"><strong>인원 : </strong>
              <span @click="togglePeopleList(item)" style="cursor: pointer;">{{ item.people.length }}명</span>
            </p>
            <div v-if="showPeopleList(item)">
              <div class="people-list">
                <div class="people-list-header">명단</div>
                <p v-for="person in item.people" :key="person.name" class="people-list-item">{{ person.name }} ({{
                  person.department }} - {{ person.team }})</p>
              </div>
              <hr />
            </div>
            <p class="card-text"><strong>구분/사유 : </strong> {{ item.type }} / {{ item.reason }}</p>
            <p class="card-text"><strong>금액 : </strong> {{ item.amount }}</p>
            <p class="card-text"><strong>금액/인원수 : </strong> {{ calculateAmountPerPerson(item) }}</p>
            <p>
              <strong class="card-text">영수증 사진 : </strong>
              <a class="card-text"
                @click.prevent="openPreviewModal(item.receipt)"
                style="cursor: pointer; color: blue;"
              >
                {{ item.receiptName }}
              </a>
            </p>
          </div>
        </div>
      </div>

      <!-- 페이지네이션 (공통 컴포넌트) -->
      <DefaultPagination
        :currentPage="currentPage"
        :totalPages="totalPages"
        :visiblePageCount="visiblePageCount"
        @pageChange="onPageChange"
      />
    </div>

    <!-- 사이드바 -->
    <div :class="['sidebar-wrapper', { 'sidebar-visible': sidebarVisible }]">
      <ReceiptSidebar 
        v-if="!isMobile" 
        :item="selectedItem" 
        :visible="sidebarVisible" 
        @close="closeSidebar"
        @preview-image="openPreviewModal"
      />
    </div>

    <!-- 이미지 미리보기 모달 -->
    <div v-if="isPreviewVisible" class="modal preview-modal" @click="closePreviewModalOnOutsideClick">
      <div
        class="preview-modal-content"
        @mousedown="startDrag"
        @mousemove="onDrag"
        @mouseup="endDrag"
        @mouseleave="endDrag"
        @touchstart="startDrag"
        @touchmove="onDrag"
        @touchend="endDrag"
      >
        <img
          :src="previewImage"
          :class="{ zoomed: isZoomed }"
          class="preview-modal-image"
          :style="{
            transform: isZoomed
              ? `translate(${zoomedPosition.x}px, ${zoomedPosition.y}px) scale(1.5)`
              : 'none',
            transformOrigin: `${zoomOrigin.x}px ${zoomOrigin.y}px`
          }"
          @dblclick="toggleZoom"
          @touchstart="toggleZoom"
        />
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted } from 'vue';
import * as bootstrap from 'bootstrap';
import ReceiptSidebar from './ReceiptSidebar.vue';
import DefaultTable from '@/components/common/table/DefaultTable.vue';
import DefaultButton from '@/components/common/button/DefaultButton.vue';
import ReceiptSubmissionCreateModal from '@/components/receipt/ReceiptSubmissionCreateModal.vue';
import DefaultPagination from '@/components/common/pagination/DefaultPagination.vue';

import { usePreviewModal } from '@/utils/preview-modal';
// import axios from 'axios'; // 실제 백엔드 호출 시 사용

const {
  isPreviewVisible,
  previewImage,
  isZoomed,
  zoomedPosition,
  zoomOrigin,
  openPreviewModal,
  toggleZoom,
  startDrag,
  onDrag,
  endDrag
} = usePreviewModal();

// 사이드바
const selectedItem = ref(null);
const sidebarVisible = ref(false);

// 반응형 여부
const isMobile = ref(window.innerWidth <= 1200);
const updateIsMobile = () => {
  isMobile.value = window.innerWidth <= 1200;
};

// 페이지네이션 관련 상태
const currentPage = ref(1);
const totalPages = ref(1); // 백엔드로부터 받아오는 총 페이지 수
const visiblePageCount = ref(5); // 페이지네이션에서 보여질 페이지 수

// 실제 데이터
const data = ref([]); // 화면에 뿌릴 데이터 (백엔드에서 받아올 것)
const columns = [
  { key: 'date', label: '발생일', width: 100  },
  { key: 'type', label: '구분', width: 50  },
  { key: 'peopleCount', label: '인원수', width: 60  },
  { key: 'reason', label: '사유', width: 200  },
  { key: 'amount', label: '금액', width: 60  },
  { key: 'amountPerPerson', label: '금액/인원수', width: 100  }
];

/** 
 * 페이지 변경 시 호출될 함수
 * - 백엔드에 page(또는 offset 등)를 보내서 해당 페이지 데이터를 받는다.
 * - 응답으로 현재 페이지 데이터와 totalPages(또는 totalItems)를 받으면 state 갱신
 */
function onPageChange(newPage) {
  currentPage.value = newPage;
  fetchDataFromServer(newPage);
}

/**
 * 백엔드에서 데이터 가져오기 (예시)
 * - 실제로는 axios를 사용해서 서버에 page, itemsPerPage 등을 넘겨주고
 *   응답으로 받은 데이터/totalPages 등을 세팅.
 */
// async function fetchDataFromServer(page = 1) {
async function fetchDataFromServer() {
  try {
    // 예시: axios.get('/api/receipts', { params: { page, itemsPerPage } })
    // const response = await axios.get('/api/receipts', { params: { page, itemsPerPage } });
    // data.value = response.data.items;
    // totalPages.value = response.data.totalPages;

    // 여기서는 예시 데이터를 생성
    const mockData = [
      {
        date: '2024-08-01',
        type: '식비',
        reason: '야근',
        amount: '100,000원',
        receiptName: '영수증 예시',
        receipt: 'https://kwon-beom-jun.github.io/centrally-system-page/img/receipt_example.jpg',
        people: [
          { department: 'SI사업본부', team: '프레임워크', name: '권범준', position: '책임' },
          { department: 'SI사업본부', team: '프레임워크', name: '홍길동', position: '선임' }
        ],
      },
      {
        date: '2024-08-02',
        type: '식비',
        reason: '야근',
        amount: '120,000원',
        receiptName: '영수증 예시',
        receipt: 'https://kwon-beom-jun.github.io/centrally-system-page/img/receipt_example.jpg',
        people: [
          { department: 'SI사업본부', team: '프레임워크', name: '권범준', position: '책임' },
          { department: 'SI사업본부', team: '프레임워크', name: '홍길동', position: '선임' },
          { department: 'SI사업본부', team: '프레임워크', name: '이석순', position: '선임' },
        ],
      },
      {
        date: '2024-08-02',
        type: '식비',
        reason: '야근',
        amount: '120,000원',
        receiptName: '영수증 예시',
        receipt: 'https://kwon-beom-jun.github.io/centrally-system-page/img/receipt_example.jpg',
        people: [
          { department: 'SI사업본부', team: '프레임워크', name: '권범준', position: '책임' },
          { department: 'SI사업본부', team: '프레임워크', name: '홍길동', position: '선임' },
          { department: 'SI사업본부', team: '프레임워크', name: '이석순', position: '선임' },
        ],
      },
      // ...
    ];

    // 실제로는 페이지 번호에 따라 다르게 받아오겠지만, 여기선 단순히 예시
    data.value = mockData.map(item => ({
      ...item,
      peopleCount: item.people.length,
      amountPerPerson: calculateAmountPerPerson(item)
    }));

    // 총 페이지 갯수 (추후 백엔드에서 받아와야함)
    totalPages.value = 3;
  } catch (e) {
    console.error(e);
  }
}

// 금액/인원수 계산 예시
function calculateAmountPerPerson(item) {
  const amount = parseInt(item.amount.replace(/[^0-9]/g, ''), 10);
  const peopleCount = item.people.length;
  return peopleCount > 0
    ? Math.floor(amount / peopleCount).toLocaleString() + '원'
    : 'N/A';
}

// 카드 레이아웃 명단 리스트
const visiblePeopleLists = ref({});
const showPeopleList = (item) => {
  return visiblePeopleLists.value[item.date];
};
const togglePeopleList = (item) => {
  if (visiblePeopleLists.value[item.date]) {
    delete visiblePeopleLists.value[item.date];
  } else {
    visiblePeopleLists.value[item.date] = true;
  }
};

// 모달 등 나머지 로직
const modalInstance = ref(null);
function openModal() {
  const modalElem = document.getElementById('receiptModal');
  if (!modalInstance.value) {
    modalInstance.value = new bootstrap.Modal(modalElem, {
      backdrop: 'static',
      keyboard: false,
    });
  }
  modalInstance.value.show();
}
function closeModal() {
  modalInstance.value?.hide();
}
function openSidebar(item) {
  if (!isMobile.value) {
    selectedItem.value = item;
    sidebarVisible.value = true;
  }
}
function closeSidebar() {
  sidebarVisible.value = false;
}

// 모바일 화면 판단
onMounted(() => {
  window.addEventListener('resize', updateIsMobile);

  // 컴포넌트 초기 로드시 1페이지 데이터를 불러온다
  fetchDataFromServer(currentPage.value);
});

// 이미지 미리보기 모달 관련
function closePreviewModalOnOutsideClick(event) {
  if (!event.target.classList.contains('preview-modal-image')) {
    isPreviewVisible.value = false;
  }
}
</script>

<style scoped>
.content {
  transition: margin-right 0.3s ease;
}

.content-expanded {
  margin-right: 300px;
}

.create-receipt {
  margin-bottom: 20px;
}

/* 사이드바 스타일 */
.sidebar-wrapper {
  position: fixed;
  right: 0;
  top: 0;
  width: 300px;
  height: 100%;
  transform: translateX(100%);
  transition: transform 0.3s ease;
  z-index: 1000;
}
.sidebar-visible {
  transform: translateX(0);
}
.sidebar {
  width: 100%;
  height: 100%;
  background-color: #f9f9f9;
  border-left: 1px solid #ddd;
  padding: 20px;
  overflow-y: auto;
  box-shadow: -2px 0 5px rgba(0, 0, 0, 0.1);
}

/* 이미지 미리보기 모달 */
.preview-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.7);
  z-index: 9999;
  /* 돋보기 커서 */
  /* cursor: zoom-out; */
  display: flex;
  align-items: center;
  justify-content: center;
}

.preview-modal-content {
  position: relative;
}

.preview-modal-image {
  max-width: 80vw;
  max-height: 80vh;
  /* 돋보기 커서 */
  /* cursor: zoom-in; */
  transition: transform 0.2s ease;
}

.people-list {
  font-size: 0.7rem;
  max-height: 150px;
  overflow-y: auto;
  margin-top: 10px;
  border-top: 1px solid #ddd;
  padding-top: 10px;
}

.people-list-header {
  font-weight: bold;
  margin-bottom: 5px;
}

.people-list-item {
  padding: 1px 0;
  margin: 0;
  margin-left: 10px;
}

@media (max-width: 1200px) {
  .form-group {
    margin-bottom: 10px;
  }

  .form-group .mt-2 {
    margin: 0 !important;
  }

  .form-group label {
    font-size: 0.9rem;
  }

  .form-control-file {
    padding: 0px;
  }
}
</style>
