<template>
  <div class="default-textfield">
    <!--
      :type, :id, :name, :placeholder
        → 부모 컴포넌트에서 전달된 값을 그대로 바인딩하여 input의 기본 속성을 설정

      v-model="internalValue"
        → 양방향 데이터 바인딩으로, 사용자가 입력한 값이 internalValue에 저장되며, 부모의 modelValue와 동기화

      v-bind="type === 'radio' ? { value: value } : {}"
        → input의 타입이 "radio"일 경우에만 value prop을 추가하여, 라디오 버튼의 값을 지정

      :class="..."
        → input 타입이 "radio"가 아닐 때는 'form-control', 사이즈에 따른 클래스(sizeClass), 
          그리고 유효성 검사에 따른 'is-invalid' 클래스를 적용
        → 타입이 "radio"이면 빈 문자열을 적용해 기본 브라우저 스타일(라디오 버튼 스타일)을 유지

      @blur, @change
        → blur 이벤트에서 touched를 true로 설정하고, change 이벤트는 부모로 emit하여 외부에서 이벤트를 처리할 수 있도록 함

      :disabled, :required
        → 각각 비활성화(disabled) 및 필수 입력(required) 여부를 부모에서 제어할 수 있도록 바인딩
    -->
    <input
      :type="type"
      :id="id"
      :name="name"
      :placeholder="placeholder"
      v-model="internalValue"
      v-bind="type === 'radio' ? { value: value } : {}"
      :class="type !== 'radio' ? ['form-control', sizeClass, { 'is-invalid': touched && !isValid }] : ''"
      @blur="touched = true"
      @change="$emit('change', $event)"
      :disabled="disabled"
      :required="required"
    />
    <!-- 에러 메시지 영역: reserveErrorSpace 프롭에 따라 공간 확보 여부 결정 -->
    <template v-if="reserveErrorSpace">
      <!-- 에러 메시지 영역을 항상 렌더링하여 공간을 확보 -->
      <div class="error-message reserved">
        <span v-if="touched && !isValid">{{ errorMessage }}</span>
      </div>
    </template>
    <template v-else>
      <!-- 에러 메시지가 있을 때만 렌더링 -->
      <div v-if="touched && !isValid" class="error-message">
        {{ errorMessage }}
      </div>
    </template>
  </div>
</template>

<script setup>
import { computed, defineProps, defineEmits, ref } from 'vue';

const props = defineProps({
  // v-model로 사용할 값
  // 문자열이나 Date 타입을 받을 수 있으며, 기본값은 빈 문자열
  modelValue: {
    type: [String, Date],
    default: ''
  },
  // input 요소의 타입을 지정
  // 예를 들어, "text", "date", "month", "radio" 등 다양한 타입을 사용할 수 있음
  // 기본값은 "text"
  type: {
    type: String,
    default: 'text'
  },
  // input 요소의 id를 지정
  // 주로 label의 for 속성과 연계되어 접근성과 DOM 식별에 사용
  id: {
    type: String,
    default: ''
  },
  // input 요소의 placeholder(자리 표시자) 텍스트
  // 사용자가 아무런 입력을 하지 않았을 때 안내 문구로 표시됨됨
  placeholder: {
    type: String,
    default: ''
  },
  // input 요소의 넓이를 제어하는 프롭
  // 'small', 'medium', 'large', 'full' 중 하나의 값을 가지며,
  // 각각 미리 정의된 CSS 클래스(예: small-input, medium-input 등)를 적용
  size: {
    type: String,
    default: 'medium',
    validator: value => ['xsamll', 'small', 'medium', 'large', 'full'].includes(value)
  },
  /**
   * validationType 프롭:
   * 입력값의 검증 유형을 지정
   * 사용 가능한 값은 다음과 같음음
   * - ''         : 검증 없음
   * - 'english'  : 영어만 입력 가능
   * - 'number'   : 숫자만 입력 가능
   * - 'korean'   : 한글만 입력 가능
   * - 'email'    : 이메일 형식 검증 (예: user@example.com)
   */
  validationType: {
    type: String,
    default: '',
    validator: value => ['', 'english', 'number', 'korean', 'email'].includes(value)
  },
  // validationType에 따른 검증 실패 시 표시할 에러 메시지
  // 기본 메시지는 "입력 형식이 올바르지 않습니다."로 설정
  errorMessage: {
    type: String,
    default: '입력 형식이 올바르지 않습니다.'
  },
  /**
   * reserveErrorSpace 프롭:
   * true이면, 에러 메시지 영역이 항상 렌더링되어 빈 공간이라도 차지
   * false이면, 에러가 실제로 있을 때만 에러 메시지 영역이 렌더링됨
   * 이를 통해 레이아웃이 변경되지 않도록 미리 공간을 확보할 수 있음
   */
  reserveErrorSpace: {
    type: Boolean,
    default: false
  },
  // disabled 프롭:
  // true일 경우, 해당 입력 필드를 비활성화하여 사용자가 값을 변경할 수 없게 함
  disabled: {
    type: Boolean,
    default: false
  },
  // required 프롭:
  // true일 경우, HTML5의 required 속성이 적용되어 필수 입력 필드로 동작
  required: {
    type: Boolean,
    default: false
  },
  // name 프롭:
  // 주로 라디오 버튼과 같이 여러 요소가 그룹으로 묶여야 할 때 사용
  // 같은 그룹에 속하는 라디오 버튼들은 동일한 name 값을 가져야 함
  name: {
    type: String,
    default: ''
  },
  // value 프롭:
  // 라디오 버튼 등에서 사용되는 값으로, 해당 요소의 고유 값을 나타냄
  // [String, Number, Boolean] 타입을 지원
  value: {
    type: [String, Number, Boolean],
    required: false
  }
});


const emits = defineEmits(['update:modelValue', 'change']);

// v-model 양방향 바인딩을 위한 computed
const internalValue = computed({
  get() {
    return props.modelValue;
  },
  set(newValue) {
    emits('update:modelValue', newValue);
  }
});

// size 프롭에 따른 클래스 계산
const sizeClass = computed(() => {
  switch (props.size) {
    case 'xsmall':
      return 'xsmall-input';
    case 'small':
      return 'small-input';
    case 'large':
      return 'large-input';
    case 'full':
      return 'full-input';
    case 'medium':
    default:
      return 'medium-input';
  }
});

// validationType에 따른 정규식 패턴
const pattern = computed(() => {
  switch (props.validationType) {
    case 'english':
      return /^[A-Za-z]*$/;
    case 'number':
      return /^[0-9]*$/;
    case 'korean':
      return /^[ㄱ-ㅎ가-힣]*$/;
    case 'email':
      return /^[A-Za-z0-9._%+-]+@[A-Za-z0-9.-]+\.[A-Za-z]{2,}$/;
    default:
      return null;
  }
});

// 입력값의 유효성 여부 판단 (검증 대상이 없으면 true)
const isValid = computed(() => {
  if (!pattern.value) return true;
  return pattern.value.test(internalValue.value);
});

// 필드 터치 여부 (blur 이벤트에서 true로 변경)
const touched = ref(false);
</script>

<style scoped>
