  width: 500px;
  font-size: 0.8rem; 
}

.full-input {
  width: 100%;
}

.xsmall-input::placeholder,
.small-input::placeholder,
.medium-input::placeholder,
.large-input::placeholder,
.full-input::placeholder {
  font-size: 0.8rem;
}

/* 유효하지 않은 경우 빨간 테두리 적용 */
.is-invalid {
  border-color: red;
}

/* 에러 메시지 영역: reserveErrorSpace가 true일 때 적용 */
.error-message.reserved {
  min-height: 1.2rem; /* 항상 일정한 공간 확보 (원하는 높이로 조정 가능) */
  margin-top: 5px;
  color: red;
  font-size: 0.85rem;
}

/* reserveErrorSpace가 false인 경우 기본 error-message 스타일 */
.error-message {
  margin-top: 5px;
  color: red;
  font-size: 0.85rem;
}

@media (max-width: 1200px) {

  .default-textfield input[type="month"],
  .default-textfield input[type="date"] {
    min-width: 110px;
  }

  /* 사이즈에 따른 input 넓이 설정 */
  .xsmall-input { 
    font-size: 0.6rem;
    width: 80px;
    padding: 0.5rem 0.6rem !important;
    height: auto !important;
    line-height: 1.5;
  }

  .small-input {
    font-size: 0.6rem;
    width: 130px;
    padding: 0.5rem 0.6rem !important;
    height: auto !important;
    line-height: 1.5;
  }

  .medium-input {
    font-size: 0.75rem;
    width: 220px;
  }

  .large-input {
    font-size: 0.8rem;
    width: 280px;
  }

  .full-input {
    font-size: 0.9rem;
  }

  /* 모든 사이즈 placeholder 0.65rem */
  .xsmall-input::placeholder,
  .small-input::placeholder,
  .medium-input::placeholder,
  .large-input::placeholder,
  .full-input::placeholder {
    font-size: 0.65rem;
  }
}

@media (max-width: 500px) {

  .default-textfield input[type="month"],
  .default-textfield input[type="date"] {
    min-width: 90px;
  }

  /* 사이즈에 따른 input 넓이 설정 */
  .xsmall-input {
    width: 70px;
    font-size: 0.55rem;
  }

  .small-input {
    width: 98px;
    font-size: 0.55rem;
  }

  .medium-input {
    width: 150px;
    font-size: 0.55rem;
  }

  .large-input {
    width: 180px;
    font-size: 0.55rem;
  }

  .full-input {
    font-size: 0.55rem;
    padding: 0.5rem 0.6rem !important;
    height: auto !important;
    line-height: 1.5;
  }

  .default-textfield input[type="month"] {
    font-size: 0.55rem;
  }

  .default-textfield input[type="date"] {
    font-size: 0.55rem;
  }
  /* 모든 사이즈 placeholder 0.55rem */
  .xsmall-input::placeholder,
  .small-input::placeholder,
  .medium-input::placeholder,
  .large-input::placeholder,
  .full-input::placeholder {
    font-size: 0.55rem;
  }
}
</style>
  